@import 'scss_header';

.ui-dialog {
	&.dialog-full-contentdetail {
		.ui-dialog-contents {
			.dialog-contents {
				.detail-container-wrapper {
					.section {
						&.title {
							padding: 0;

							.title-img-item {
								.ratio-img-wrap {
									img {
										width: 100%;
									}
								}
							}

							.contents-col-wrap {
								.common-text-item {
									padding: 0 15px;

									.editor-wrap {
										text-align: center;
									}

									+ .title-img-item {
										padding: 30px 15px 0;
									}
								}

								.title-img-item {
									+ .common-text-item {
										padding-top: 30px;

										.editor-wrap {
											text-align: left;
										}
									}
								}
							}

							.contents-row-wrap {
								position: relative;
								overflow: hidden;

								.common-text-item {
									position: absolute;
									top: 30px;
									left: 15px;
									right: 15px;
									bottom: 40px;
									z-index: 2;
								}

								&.left-img {
									.editor-wrap {
										text-align: right;
									}

									.title-img-item {
										margin-right: 33.3333%;
									}
								}

								&.right-img {
									.editor-wrap {
										text-align: left;
									}

									.title-img-item {
										margin-left: 33.3333%;

									}
								}
							}
						}

						&.text {
							.common-text-item {
								&[class*=size-] {
									&:after {
										content: '';
										display: block;
										height: 0px;
										margin-top: 0px;
										background-position: bottom center !important;
									}
								}

								&.size-lg {
									&:after {
										// @include background_img('../images/detail/bg_section_text_lg.png', 660px, 124px);
									}
								}

								&.size-md {
									&:after {
										// @include background_img('../images/detail/bg_section_text_md.png', 300px, 124px);
									}
								}

								&.size-sm {
									&:after {
										// @include background_img('../images/detail/bg_section_text_sm.png', 200px, 124px);
									}
								}
							}
						}

						&.text-img {
							.flex-box {
								&.col-1 {
									.flex-box-contents {
										~ .flex-box-contents {
											margin-top: 20px;
										}
									}
								}
							}
						}

						&.button {
							.link-btn-box {
								+ .link-btn-box {
									margin-top: 21px;
								}

								&.center {
									text-align: center;
								}
							}

							.btn-box {
								+ .btn-box {
									margin-top: 15px;
								}

								&.center {
									text-align: center;
								}
							}
						}

						&.prod {
							padding: 0;

							.prod-grid-list {
								padding-top: 0;

								.prod-item {
									&:first-child {
										padding-top: 0;
									}

									&:last-child {
										padding-bottom: 0;
									}
								}

								&:not(.col-1) {
									.prod-item {
										&:nth-child(2) {
											padding-top: 0;
										}

										&:nth-last-child(-n + 2) {
											padding-bottom: 0;
										}
									}
								}
							}

							.tab-wrap {
								.tab-content {
									.prod-grid-list {
										padding-top: 15px;
									}
								}
							}

							.banner-view-wrap {
								padding: 0 15px;
								margin-bottom: 15px;

								img {
									max-width: 100%;
								}
							}
						}

						&.content-img {
							padding: 0;

							.contents-banner-wrap {
								.ratio-img-wrap {
									display: block;

									img {
										display: block;
										max-width: 100%;
										width: 100%;
										margin: 0 auto;
									}
								}
							}

							.contents-gallery-wrap {
								// 상단 배너 스와이프
								.contents-banner-wrap {
									margin: 0 15px;
								}
							}

							.pictorial-gallery-wrap {
								border-top: 1px solid $colorBlack;

								// 상단 배너
								.contents-banner-wrap {
									.contents-banner-swiper {
										.contents-banner-item {
											.view-center-area {
												.view-button {
													width: auto;
													min-width: 190px;
												}

												&.center {
													.common-text-item {
														.editor-wrap {
															text-align: center;
														}
													}

													.view-button {
														margin-left: auto;
													}
												}
											}
										}
									}
								}

								// 중간 컨텐츠
								.banner-contents-wrap {
									margin-bottom: 15px;

									.banner-contents-swiper {
										.banner-contents-item {
											.prod-item {
												&.individual {
													border-width: 1px 0;
												}
											}

											.common-text-item {
												border-width: 1px 0;
											}
										}
									}
								}

								// 썸네일
								.contents-thumbnail-wrap {
									position: relative;
									height: 100%;
									padding: 0 30px 30px;

									.contents-thumbnail-item {
										width: 50px;

										.ratio-img-wrap {
											@include justify_width(start);
											align-items: center;
											height: 100%;

											img {
												display: block;
												max-width: 100%;
												width: 100%;
												margin: 0 auto;
												opacity: .5;
											}
										}

										&:after {
											content: '';
											position: absolute;
											top: 0;
											left: 0;
											right: 0;
											bottom: 0;
											border: 1px solid $colorLightenGray;
											opacity: .5;
										}

										&.swiper-slide-thumb-active {
											.ratio-img-wrap {
												img {
													opacity: 1;
													transition: opacity .1s ease-in-out;
												}
											}

											&:after {
												border-color: $colorBlack;
											}
										}
									}

									&:after {
										content: '';
										position: absolute;
										bottom: 0;
										left: 15px;
										right: 15px;
										border-bottom: 1px solid $colorLightenGray;
									}
								}

							}

							// 이미지, 이미지 화보 공통
							.swiper-wrapper {
								&:not(.contents-banner-swiper) {
									.swiper-slide {
										.common-text-item {
											border: 1px solid $colorBlack;
											padding: 15px;
										}
									}
								}
							}
						}

						&.video {
							padding: 0;

							.video-box {
								.ratio-img-wrap {
									img {
										display: block;
										width: 100%;
									}
								}

								.common-text-item {
									position: absolute;
									top: 15px;
									left: 15px;
									right: 15px;
									bottom: 15px;
									max-height: 100%;
									height: auto;
									z-index: 10;
									pointer-events: none;
								}
							}
						}

						//s : 공통사용
						// s : 컨텐츠 이미지 스와이퍼
						.contents-banner-wrap {
							position: relative;
							height: 100%;
							background-color: $colorLightestGray;

							.contents-banner-item {
								.ratio-img-wrap {
									@include justify_width(start);
									align-items: center;
									height: 100%;
									width: 100%;

									img {
										display: block;
										max-width: 100%;
										width: 100%;
										margin: 0 auto;
									}
								}

								.banner-view-wrap {
									@include justify_width;
									height: 100%;
									align-items: center;

									//s : 에디터/버튼영역
									.view-center-area {
										position: absolute;
										top: 30px;
										bottom: 40px;
										left: 15px;
										right: 15px;
										z-index: 2;
										@include justify_width(start);
										flex-direction: column;

										.common-text-item {
											height: auto;
											max-height: 100%;

											.editor-wrap {
												width: 100%;
											}
										}

										.view-button {
											margin-top: 20px;
											margin-right: auto;
										}
									}

									//e : 에디터/버튼영역
								}
							}

							.swiper-scrollbar {
								+ .contents-banner-count-wrap {
									padding-bottom: 63px;
								}
							}

							.contents-banner-count-wrap {
								position: absolute;
								width: 100%;
								background: rgba(#fff, .5);
								padding: 30px;
								font-family: $fontEng;
								left: 0;
								bottom: 0;
								z-index: 2;

								.contents-count-title {
									text-align: center;
									text-transform: uppercase;
									font-size: 1.6rem;
								}

								.contents-count-wrap {
									margin-top: 23px;
									text-align: center;

									.count-item {
										display: inline-block;
										min-width: 38px;

										.count-value, .count-desc {
											display: block;
											text-align: center;
										}

										.count-value {
											font-size: 3.2rem;
										}

										.count-desc {
											margin-top: 8px;
											font-size: 1.2rem;
										}
									}
								}
							}
						}

						// e : 컨텐츠 이미지 스와이퍼

						// s : 연관상품 스와이퍼
						.related-prod-wrap {
							margin-top: 15px;
							padding: 0 15px;

							.related-prod-swiper {
								.related-prod-item {
									width: 120px;

									.prod-item {
										padding: 0;

										.info-wrap {
											.prod-price-wrap {
												.prod-price-value {
													.prod-price-normal {
														text-decoration: initial;
													}
												}

												.discount-percent {
													align-self: flex-end;
												}
											}
										}
									}

									&.swiper-slide-prev {
										transform: translateX(-5px);
									}
								}
							}

							.prod-item {
								&.individual {
									~ .prod-item {
										border-top: none;
									}
								}
							}
						}

						// e : 연관상품 스와이퍼

						// s : 에디터 공통
						.common-text-item {
							overflow: hidden;
						}

						// e : 에디터 공통

						// s : 이미지 가로형
						.prod-item {
							&.individual {
								height: 90px;
								border: 1px solid $colorBlack;
								padding: 14px;

								.ratio-img-wrap {
									width: 60px;
									height: 60px;

									.img-viewer {
										padding-top: 100%;
									}

									+ .info-wrap {
										width: calc(100% - 60px);
										padding-left: 10px;
									}
								}

								.info-wrap {
									width: 100%;
									padding-left: 0;

									.prod-brand-name {
										transform: translateY(0);
									}

									.prod-name {
										@include ellipsis;
										margin-top: 4px;
										font-size: $bodyText6;
										line-height: 1.6rem;
										color: $colorDarkGray;
									}

									.prod-price-wrap {
										.prod-price-value {
											.prod-price {
												margin-right: 5px;
												font-size: $bodyText5;
											}
										}

										.discount-percent {
											margin-left: 9px;
											font-size: 1.2rem;
											line-height: 1.5rem;
											font-weight: 400;
										}
									}
								}
							}
						}

						// e : 이미지 가로형

						//s : 출처 에디터
						.source-text {
							margin-top: 8px;
							text-align: center;
						}

						//e : 출처 에디터

						//s : 버튼
						.view-link {
							display: inline-block;
							font-size: $bodyText3;
							color: $colorBlack;
							border-bottom: 1px solid $colorBlack;
							vertical-align: top;
							line-height: $bodyText3;
						}

						.view-button {
							position: relative;
							display: inline-block;
							width: 100%;
							height: 40px;
							padding: 0 50px 0 15px;
							border: 1px solid $colorBlack;
							font-family: $fontMix;
							font-size: $bodyText4;
							line-height: 38px;
							vertical-align: top;
							text-align: left;
							background-color: #fff;

							&:after {
								content: '';
								display: block;
								position: absolute;
								top: 4px;
								right: 9px;
								width: 30px;
								height: 30px;
								@include background_img('../images/detail/detail_button_arr.png', 60px, 60px);
							}
						}

						//e : 버튼

						//e : 공통사용
					}
				}
			}
		}
	}
}