@import 'scss_header';

.main-text-wrap, .exclusive-text-wrap {
	overflow: hidden;
	padding: 20px 15px;

	.editor-wrap {
		font-size: 3.2rem;
		line-height: 4rem;
	}

	.brand-list {
		li {
			overflow: hidden;
			display: inline-block;
			padding: 0 6px;

			a { 
				display: block;
				font-size: 3.2rem;
				line-height: 4rem;
				transform: translate3d(0, 40px, 0);
				-webkit-transform: translate3d(0, 40px, 0);
				-moz-transform: translate3d(0, 40px, 0);
				-ms-transform: translate3d(0, 40px, 0);
				transform-style: preserve-3d;
				-moz-transform-style: preserve-3d;
				-webkit-transform-style: preserve-3d;

				&:hover, &:active {
					text-decoration: underline;
				}
			}

			$columnCnt: 100;

			@for $i from 1 through $columnCnt {
				&:nth-child(#{$i}) {
					a {
						transition: transform #{$i*0.07}s ease;
					}
				}
			}
		}
	}

	&.is-animate {
		.brand-list {
			li {
				a {
					transform: translate3d(0, 0, 0);
					-webkit-transform: translate3d(0, 0, 0);
					-moz-transform: translate3d(0, 0, 0);
					-ms-transform: translate3d(0, 0, 0);
				}
			}
		}
	}
}

.exclusive-text-wrap {
	padding: {
		left: 9px;
		right: 9px;
	}
	border-bottom: 1px solid $colorBlack;
}

.main-wrap {
	> .swiper-wrapper {
		> .swiper-slide {
			border-top: 1px solid transparent;
			&:first-child {
				border-top-color: $colorBlack;
			}

		}
	}
	&:not(.swiper-container-horizontal) {
		> .swiper-wrapper {
			> .swiper-slide {
				display: none;
			}
		}
	}

	// s : 메인 공통
	.btn-main-more {
		display: inline-block;
		width: 190px;
		height: 50px;
		padding: 9px 0 9px 50px;
		box-shadow: -10px 10px 10px 0 rgba(0, 0, 0, 0.05);
		background: {
			image: url(../images/ui/button/ui_btn_detail_arrow.png);
			repeat: no-repeat;
			position: 10px 10px;
			size: 30px;
			color: #fff;
		}
		text-align: left;

		span {
			display: block;
			color: $colorGray;
			font-size: 1rem;
			line-height: 1.5rem;

			&.eng {
				color: $colorBlack;
				font-size: 1.2rem;
				font-weight: 500;
				letter-spacing: -.3px;
			}

			+ span {
				margin-top: 2px;
			}
		}
	}

	// e : 메인 공통

	// s: 메인 For you
	.main-personalize-banner {
		position: relative;
		border-bottom: 1px solid $colorBlack;

		img {
			width: 100%;
			max-width: 100%;
		}

		.btn-main-more {
			position: absolute;
			right: 0;
			bottom: 20px;
		}
	}

	.main-visual-banner-wrap {
		.ratio-img-wrap .img-viewer {
			padding-top:59.1666%;
		}
		.swiper-page-number {
			padding:4px 8px;
			left:10px;
			bottom:50px;
			top:inherit;
			right:inherit;

			>span {
				font-size:1.2rem;
				position:relative;
				z-index:1;
			}

			&:before {
				display:block;
				width:100%;
				height:100%;
				content:"";
				background: #000;
				opacity: 0.5;
				position:absolute;
				left:0;
				top:0;
				z-index:0;
				border-radius:18px;
			}
			
		}
		.banner-info-wrap {
			display:block;
			padding:0 10px;
			background:#fff;
			text-align:center;
			font-size:1.2rem;
			height:40px;
			line-height:40px;
			border:1px solid #000;
			border-width:1px 0;

			.banner-text {
				display:block;
				text-overflow: ellipsis;
				white-space: nowrap;
				overflow: hidden; 
			}
		}
		
	}


	.main-prod-list-wrap {
		.prod-grid-list {
			padding: 0;

			.prod-item {
				position: relative;
				width: 33.3333%;
				padding: 0;
				margin-top: 0;
				border-bottom: 1px solid $colorBlack;

				&.col-2 {
					width: 66.6666%;

					.prod-link {
						height: 100%;

						.video-box, .ratio-img-wrap {
							height: 100%;

							.img-viewer {
								width: 100%;
								height: 100%;
								padding: 0;
							}
						}
					}
					.banner-text-wrap {
						padding:0 10px;
						height:37px;
						background:#fff;
						display:block;
                		text-overflow: ellipsis;
						white-space: nowrap;
						overflow: hidden; 
						font-size:12px;
						color:#000;
						.banner-text {
							display:inline;;
							margin: 0;
							padding: 0;
							line-height:37px;
							background: none;
							&+ .banner-text:before {
								margin: 2px;
								content: '/';
							}
						}
					}
				}

				&:after {
					content: '';
					position: absolute;
					display: block;
					width: 1px;
					top: 0;
					right: 0;
					bottom: 0;
					background: $colorBlack;
					z-index: 1;
				}

				.info-wrap {
					display: flex;
					flex-flow: column;

					.prod-name {
						color: $colorDarkGray;
						margin-top: 11px;
						padding: 0 10px;
						font-weight: 300;
						line-height: 1.8rem;
						@include ellipsis;
					}

					.prod-price-wrap {
						margin-top: 5px;
						padding-left: 10px;
						padding-right: 10px;

						.prod-price-value {
							.prod-price {
								margin-right: 5px;
								font-size: 1.2rem;
								line-height: 15px;
							}

							.prod-price-normal { 
								display: none;
								text-decoration: none;
							}
						}
					}

					.prod-brand-name {
						@include ellipsis;
						display: block;
						padding: 0 10px;
						border-top: 1px solid $colorLightestGray;
						line-height: 3.6rem;
						order: 3;
					}

					.auto-badge-wrap {
						display: none;
						padding-left: 10px;
					}
				}
			}
		}
	}

	// e: 메인 For you

	// s : 메인 Today
	.main-insider-wrap {
		margin: 15px 0;
		padding: 0 35px 0 15px;
	}

	.main-today-box {
		border: {
			top: 1px solid $colorBlack;
			bottom: 1px solid $colorBlack;
		}

		.today-box-title {
			padding: 15px;
			font-size: 1.8rem;
			font-weight: 500;
			line-height: 2rem;
		}

		.main-recommend-wrap {
			padding: 0 30px 30px 15px;
		}

		.thumb-list-swiper-wrap {
			padding: 0 7.43% 15px 15px;
		}

		.main-today-img-video-box {
			background: $colorLightenGray;

			img {
				max-width: 100%;
				width: 100%;
			}

			.swiper-slide {
				align-self: center;
			}
		}

		.main-today-bottom-box {
			position: relative;

			.profile-flex-wrap {
				padding: 15px 10px;

				.profile-name .profile-detail .profile-text {
					font-weight: 300;
				}
			}

			.today-info-wrap {
				padding: 16px 15px 30px;
				border-top: 1px solid $colorLightestGray;

				.today-info-title {
					font-size: $bodyText2;
					line-height: 2.2rem;
					@include ellipsis;
				}

				.more-text-wrap {
					margin-top: 6px;
				}
			}

			.btn-today-product, .btn-main-more {
				z-index: 5;
			}

			.btn-today-product {
				position: absolute;
				color: #fff;
				background: {
					color: rgba(0, 0, 0, .5);
					image: url(../images/ui/button/ui_btn_detail_plus.png);
					repeat: no-repeat;
					position: 11px 11px;
					size: 12px;
				}
				border-radius: 18px;
				padding: 11px 12px 11px 33px;
				text-transform: uppercase;
				font-size: 1rem;
				line-height: 1.3rem;
				letter-spacing: 0.5px;

				left: 15px;
				top: -50px;

				&:before {
					content: '';
				}
			}

			.btn-main-more {
				position: absolute;
				right: 0;
				top: -40px;
			}
		}

		~ .main-today-box {
			margin-top: 15px;
		}

		&:last-of-type {
			border-bottom: none;
		}

		.js-today-img-swiper {
			~ .main-today-bottom-box {
				.btn-today-product {
					top: -65px;
				}

				.btn-main-more {
					top: -80px;
				}
			}
		}
	}

	// e : 메인 Today
}

.brand-select-wrap {
	height: calc(100% - 55px);
	overflow-y: auto;

	.brand-select-title-wrap {
		margin: 30px 15px;

		.brand-select-title, .brand-select-desc {
			text-align: center;
		}

		.brand-select-title {
			font-size: 1.6rem;
			line-height: 2.4rem;
		}

		.brand-select-desc {
			margin-top: 5px;
			font-size: $bodyText5;
			line-height: 1.8rem;
		}
	}

	.brand-select-list, .gender-select-list {
		padding: 0 7.5px;
		@include justify_width(start);
		flex-wrap: wrap;

		.brand-select-item, .gender-select-item {
			position: relative;
			flex: 0 0 33.3333%;
			padding: 0 7.5px;
			margin-bottom: 15px;

			input[type=checkbox], input[type=radio] {
				position: absolute;
				width: 1px;
				height: 1px;
				padding: 0;
				margin: -1px;
				overflow: hidden;
				clip: rect(0, 0, 0, 0);
				border: 0;

				+ label {
					display: block;
				}

				&:checked {
					+ label {
						.brand-img, .gender-img {
							&:before, &:after {
								opacity: 1;
							}
						}
					}
				}
			}

			label {
				.brand-img, .gender-img {
					position: relative;
					display: block;

					&:before, &:after {
						content: '';
						position: absolute;
						display: inline-block;
						left: 0;
						top: 0;
						opacity: 0;
						-webkit-transition: opacity $transDefaultTime $transDefaultEase;
						transition: opacity $transDefaultTime $transDefaultEase;
					}

					&:before {
						width: 100%;
						height: 100%;
						background: rgba(0, 0, 0, .7);
						border-radius: 50%;
					}

					&:after {
						width: 100%;
						height: 100%;
						background: url(../images/ui/button/ui_btn_check.png) no-repeat center;
						background-size: 30px;
					}

					img {
						width: 100%;
						max-width: 100%;
						border-radius: 50%;
					}
				}

				.brand-name {
					margin-top: 5px;
					padding: 0 10px;
					text-align: center;
					font-weight: 700;
					@include multi_ellipsis(12px);
				}
			}
		}

	}

	.brand-select-list {
		@include device_horizontal {
			padding: 0;
			margin: 0 auto;
			width: 345px;
		}
	}

	.gender-select-list {
		margin-bottom: 30px;
		position: relative;

		&:after {
			content: '';
			display: block;
			bottom: 0;
			left: 15px;
			right: 15px;
			height: 1px;
			position: absolute;
			background: $colorLightestGray;
		}

		@include device_horizontal {
			justify-content: center;
			-webkit-justify-content: center;
			.gender-select-item {
				width: 115px;
				flex: 0 0 115px;
			}
		}
	}
}
