@import 'scss_header';
@import url('reset.css');
@import url('font.css');

html, body {
	min-height: 100%;
	height: 100%;
}

html, body, input, button, textarea, select, a {
	font-family: $fontKor;
}

a, button, input, select, textarea {
	&:focus {
		outline: none;
	}
}

.wrap {
	padding: 0 0 0;
	color: $colorBlack;
	font-size: 0;
	min-height: 100vh;

	.wrap-inner {

	}

	&.menu-opened {
		.wrap-inner {
			z-index: 10;
		}
	}

	.show-mobile {
		a {
			display: block;
			color: #fff;
			background: $colorBlack;
			text-align: center;
			font-size: 1.4rem;
			line-height: 5rem;
		}
	}
}

.mobile-only {
	display: none;

	@at-root .mobile-pc & {
		display: inherit;
	}
}


@import 'layout_header';
@import 'layout_contents';
@import 'layout_footer';
@import 'layout_detail';

// 컨텐츠 화면
@import 'contents_common';
