@import 'scss_header';

/* s: layout footer */
.footer-wrap {
	position: relative;
	padding: 0 0 20px;
	border-top: 1px solid $colorBlack;
	background-color: $colorLightestGray;

	@at-root .wrap .wrap-inner .mheader-wrap ~ & {
		margin-top: -1px;
	}
	@at-root .wrap.has-footer-padding .bottom-fix-nav ~ & {
		padding-bottom: 75px;
	}

	// s: sns list
	.sns-link-box {
		position: relative;
		padding: 5px 7px 5px 8px;

		.sns-link {
			display: inline-block;
			padding: 5px;
			margin: 0 3px 0 2px;

			&:before {
				content: '';
				width: 20px;
				height: 20px;
				@include background_img('../images/common/comm_footer_sns.png', 206px, 40px);
				display: inline-block;
			}

			span {
				@include offscreen;
			}

			&.facebook:before {
				background-position: -21px 0;
			}

			&.instagram:before {
				background-position: -42px 0;
			}

			&.pcver:before {
				width: 40px;
				background-position: -64px 0;
			}
		}
	}

	// e: sns list

	// s: fold-box-list
	.fold-box-list {
		border-top: 1px solid #ddd;

		.fold-box {
			.toggle-title-box {
				border-bottom-color: #ddd;

				.toggle-title {
					text-transform: uppercase;
					font-size: 1.3rem;
					line-height: 2.2rem;
				}
			}

			&.expanded {
				.fold-box-header {
					.toggle-title-box {
						border-bottom-color: #eaeaea;
					}
				}

				.fold-box-contents {
					border-bottom-color: #ddd;
					padding: 12px 15px 7px;
				}
			}

			.fold-box-contents {
				.sub-menu-list {
					.list-item {
						padding: 1px 0 2px;

						.sub-title {
							font-size: 1.3rem;
							line-height: 2.2rem;
						}
					}
				}

				.company-info-wrap {
					padding: 3px 0;

					.company-name {
						font-size: 1.3rem;
						line-height: 2.2rem;
					}

					.company-datail-info {
						margin-top: 7px;

						.item, .gap {
							display: inline-block;
							font-size: 1.1rem;
							line-height: 2rem;
						}

						.gap {
							width: 11px;
							text-align: center;
						}

						.footer-anchor {
							margin-left: 5px;
							font-size: 10px;
							color: #333;
							text-decoration: underline;
						}
					}
				}
			}
		}
	}

	// e: fold-box-list

	// s: footer-link-list
	.footer-link-list {
		margin-top: 20px;
		text-align: center;

		li {
			display: inline-block;

			.footer-link {
				display: inline-block;
				font-size: 1.1rem;
				line-height: 1.8rem;
				vertical-align: top;

				&.family {
					position: relative;
					padding-right: 12px;

					&:after, &:before {
						content: '';
						position: absolute;
						top: 8px;
						right: 0;
						display: block;
						width: 7px;
						height: 1px;
						background: $colorBlack;
					}

					&:after {
						width: 7px;
						transform: rotate(90deg);
					}
				}
			}

			~ li {
				margin-left: 15px;
				@include mob_small {
					margin-left: 7px;
				}
			}
		}
	}

	// e: footer-link-list

	.copyright {
		margin-top: 10px;
		font-size: 1rem;
		font-weight: 300;
		line-height: 1.8rem;
		text-align: center;
	}

}

.company-intro-wrap {
	word-break: keep-all;
	word-wrap: break-word;

	.ratio-img-wrap {
		&.intro-img {
			.img-viewer {
				padding-top: 57.222%;

				@include device_horizontal {
					padding-top: 412px;
				}
			}
		}

		&.ceo-img {
			padding-top: 15px;

			.img-viewer {
				background: {
					color: transparent;
					size: auto 100%;
				}
				padding-top: 83.333%;

				@include mob_middle {
					padding-top: 600px;
				}

				@include device_horizontal {
					padding-top: 300px;
				}

			}
		}

		&.ceo-sign-img {
			padding-top: 15px;

			.img-viewer {
				overflow: hidden;
				max-height: 97px;
				background: {
					color: transparent;
					size: auto 100%;
					position: top right;
				}
				padding-top: 29.3939%;

			}
		}
	}

	.ceo-sign-img {
		margin: {
			left: auto;
			top: 30px;
		}
		width: 180px;
		text-align: right;

		img {
			width: 100%;
		}
	}

	.intro-desc {
		padding: 30px 0;
		font-size: $bodyText4;
		line-height: 2.2rem;
	}

	.history-info-wrap {
		border-top: 1px solid $colorBlack;

		.history-info-item {
			margin-top: 30px;
			@include justify_width;
			align-items: center;

			.history-title {
				width: 54px;
				font-size: $bodyText3;
				line-height: 1.9rem;
			}

			.history-list {
				width: calc(100% - 54px);
				padding-left: 20px;
				border-left: 1px solid $colorLightenGray;

				li {
					@include justify_width;

					.history-stitle {
						width: 56px;
						font-size: $bodyText4;
						line-height: 1.9rem;
					}

					.history-desc {
						width: calc(100% - 56px);
						padding-left: 5px;
						font-size: $bodyText4;
						line-height: 1.9rem;
						font-weight: 300;
						color: $colorDarkGray;

					}

					~ li {
						margin-top: 8px;
					}
				}
			}
		}
	}

	.ceo-desc {
		margin-top: 30px;
		font-size: $bodyText2;
		font-weight: 500;
		line-height: 1.47;
	}

	.ceo-sdesc {
		margin-top: 30px;
		font-size: $bodyText4;
		line-height: 1.69;
	}

	.brand-intro-list {
		padding-top: 15px;

		.brand-item {
			.ratio-img-wrap {
				.img-viewer {
					padding-top: 57.5757%;

					@include device_horizontal {
						padding-top: 190px;
					}

					@media screen and (min-width: 813px) {
						padding-top: 57.5757%;
					}


				}
			}

			[class*=brand-intro-] {
				line-height: 2rem;
			}

			.brand-intro-title {
				margin-top: 15px;
				font-size: 1.6rem;
				font-weight: 500;
			}

			.brand-intro-stitle {
				margin-top: 6px;
				font-size: $bodyText3;
				font-weight: 300;
				color: $colorGray;
			}

			.brand-intro-desc {
				margin-top: 10px;
				font-size: $bodyText4;
				text-transform: uppercase;
			}

			~ .brand-item {
				margin-top: 30px;
			}
		}
	}

	.platform-intro-list {
		padding-top: 15px;

		.platform-item {
			margin-top: 30px;

			.ratio-img-wrap {
				.img-viewer {
					padding-top: 32.7272%;
				}
			}

			.platform-intro-desc {
				margin-top: 15px;
				font-size: $bodyText4;
				line-height: 2rem;
			}
		}

	}

	.btn-wrap {
		margin-top: 30px;

		.btn {
			width: 100%;

			~ .btn {
				margin-top: 5px;
			}
		}
	}
}