@import 'scss_header';
// s : 헤더
.header-wrap {
	position: fixed;
	width: 100%;
	left: 0;
	top: 0;
	z-index: 10;


	&.no-util-btn {
		.header-box {
			padding: 0 45px;

			.btn-box {
				position: absolute;
				top: 50%;
				left: 15px;
				margin-top: -16px;
				width: 30px;
			}

			.util-btn-box {
				display: none;
			}
		}

		~ .contents-wrap {
			min-height: calc(100vh - 288px);
		}
	}

	.header-box {
		height: 45px;
		padding: 0 15px 0 11px;
		background: #fff;
		@include justify_width;
		align-items: center;
		border-bottom: 1px solid $colorBlack;

		.title-box {
			flex: 1 0 auto;
			width: calc(100% - 160px);

			.ptitle {
				@include ellipsis;
				display: block;
				line-height: 2.2rem;
				@include mob_small {
					font-size: 1.4rem;
				}
			}

			~ .util-btn-box {
				overflow: hidden;
				width: 75px;
				@include mob_small {
					width: 70px;
				}
			}
		}

		.util-btn-box {
			text-align: right;

			.hbtn-text {
				line-height: 2.2rem;
				@include mob_small {
					font-size: 1.4rem;
				}
			}
		}

		.btn-box {
			overflow: hidden;
			width: 75px;
			@include mob_small {
				width: 70px;
			}
		}


		&.mod-black {
			background: #000;
			.hbtn-arrow-back {
				&:before {
					background-image: url("../images/ui/button/ui_header_btn_back_black.png");
				}
			}
		}
	}

	/* s: 메인 header */
	.mheader-box {
		height: $headerHeightMain;
		background-color: #fff;
		border-bottom: 1px solid $colorBlack;

		[class*=mheader-] {
			@include justify_width;
			border-top: 1px solid $colorBlack;
		}

		.mheader-bottom {
			.mbtn {
				width: 50%;
			}
		}

		.mbtn {
			padding: 11px 5px 10px;
			min-width: 86px;
			font-size: 1.4rem;
			font-weight: 300;
			line-height: 1.8rem;
			color: $colorBlack;
			text-transform: uppercase;
			text-align: center;

			&.is-active {
				background: $colorBlack;
				color: #fff;
			}
		}

		// s : 스페셜 배너
		.special-banner-wrap {
			flex: 1;
			position: relative;
			height: $specialBannerHeight;
			overflow: hidden;
			border-right: 1px solid $colorBlack;

			.special-banner-link {
				display: block;
				overflow: hidden;
				height: $specialBannerHeight;
			}

			.special-banner-marquee {
				line-height: #{ $specialBannerHeight + 2 };
				font-size: 1rem;
				font-weight: 300;
				letter-spacing: 0.5px;

			}

			.btn-close {
				position: absolute;
				top: 50%;
				right: 5px;
				width: 20px;
				height: 20px;
				margin-top: -10px;

				span {
					@include offscreen;
				}

				&:before, &:after {
					content: '';
					position: absolute;
					top: 4px;
					left: 50%;
					display: block;
					width: 1px;
					height: 13px;
					background: $colorBlack;
				}

				&:before {
					transform: rotate(45deg);
				}

				&:after {
					transform: rotate(-45deg);
				}
			}
		}
	}

	/* e: 메인 header */
}

// e : 헤더


