@import 'scss_header';

.archive-list-wrap {
	padding-top: 19px;

	.archive-content {
		padding-bottom: 15px;

		.archive-header {
			@include justify_width(start);
			/*height: 70px;*/
			padding-top: 30px;
			margin: 0 15px;
			border-top: 1px solid $colorLightenGray;
			word-break: keep-all;
			word-wrap: break-word;

			.month, .keyword {
				display: block;
				font-size: 3.2rem;
				letter-spacing: -0.08rem;
				line-height: 4rem;
				color: $colorBlack;
			}

			.keyword {
				position: relative;
				display: block;
				color: $colorBlack;
				font-style: italic;
				font-weight: 300;
				padding-left: 30px;
				word-break: break-word;
				word-wrap: break-word;

				&:before {
					content: '/';
					position: absolute;
					top: 0;
					left: 0;
					padding: 0 8px;
					color: $colorBlack;
					font-style: initial;
				}
			}
		}

		.archive-swiper-container {
			margin-top: 15px;
			padding: 0 15px;

			.swiper-wrapper {
				.swiper-slide {
					width: 240px;

					.archive-item-box {
						.archive-desc {
							font-weight: 500;
							color: $colorBlack;
						}

						.profile-name {
							padding: 10px 0;
							border-bottom: 1px solid $colorLightestGray;
						}
					}
				}
			}
		}

		&:first-child {
			padding-bottom: 0;

			.archive-header {
				padding-top: 0;
				margin: 0;
				border-top: none;
				/*height: 40px;*/

			}
		}

		&:last-child {
			padding-bottom: 0;

			.archive-item-box {
				padding-bottom: 0;
			}
		}
	}

	.no-data {
		margin-top: -19px;
	}
}

.detail-cont-box {
	position: relative;

	.btn-gate-detail {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		background: rgba(255, 0, 0, 0);
		z-index: 2;
	}
}