@import 'scss_header';

.ui-dialog {
	&.dialog-full-contentdetail {
		.ui-dialog-titlebar {
			padding: 0;

			.ui-dialog-title {
				display: none;

			}

			.ui-dialog-titlebar-close {
				top: 13px;
			}
		}

		.ui-dialog-contents {
			.dialog-title {
				height: $headerHeightDetail;
				padding: {
					left: 50px;
					right: 12px;
				}
			}

			.dialog-contents {
				height: calc(100% - #{$headerHeightDetail});

				.detail-container-wrapper {
					.detail-contents-wrap {
						padding-top: 15px;

						.section {
							padding: 0 15px;

							&:first-child { // 첫번째 코너
								.common-text-item { // 에디터 영역인 경우 여백 30
									&:first-of-type {
										padding-top: 15px;
									}
								}

								&.title, &.text-img {
									.contents-col-wrap {
										.common-text-item { // 에디터 영역인 경우 여백 30
											&:first-of-type {
												padding-top: 15px;
											}
										}
									}
								}
							}

							&.html {
								padding: 0;
							}

							~ .section {
								margin-top: 40px;
							}
						}
					}

					.detail-footer-wrap {
						margin-top: 40px;

						// s: comment
						.comment-wrap {
							margin-top: 40px;

							.comment-write-box {

								.write-inner {
									border: 1px solid $colorBlack;
									@include justify_width;
									flex-wrap: wrap;
									align-items: center;
									flex: 1;
								}

								.comment-input {
									flex: 1;
									position: relative;
									@include justify_width;
									flex-wrap: wrap;
									align-items: center;
								}

								.ip-byte-check {
									position: relative;
									flex: 1;
									border: 0;
									border-right: 1px solid $colorBlack;
									@include justify_width(center);
									flex-wrap: wrap;
									height: auto;

									.byte-check-textarea {
										flex: 1;
										min-width: auto;
										padding: 12px 16px 10px 9px;
										height: 38px;
										font-family: $fontMix;
										border: 0;
										transition: height .1s ease-in-out;
									}

									.byte {
										padding: 16px 9px 0 0;
										border: none;
										font-size: 1rem;
										letter-spacing: -0.025rem;
									}
								}

								.btn-gif-open {
									width: 35px;
									height: 25px;
									margin: 7px 8px 0 0;
									text-align: center;
									background: $colorLightestGray;
									font-size: $bodyText6;
									letter-spacing: -0.028rem;
									font-family: $fontEng;
									transition: background-color 0.5s;
								}

								.gif-img-wrap {
									position: relative;
									border: 1px solid $colorBlack;
									border-top: none;
									background: $colorLightestGray;
									height: 66px;
									padding: 5px;

									.gif-wrap {
										height: 55px;
										margin-right: 90px;

										img {
											display: block;
											height: 100%;
										}
									}


									.btn-link {
										z-index: 1;
										position: absolute;
										top: 17px;
										right: 5px;
										padding: 10px;
										font-size: $bodyText6;
										text-decoration: underline;
									}
								}

								.btn-write {
									width: 38px;
									height: 38px;
									@include background_img('../images/common/comm_detail_btn_write.png', 60px, 60px);
									background-color: #fff;
									background-position: center;

									span {
										@include offscreen;
									}
								}

								.gif-list-box {
									display: none;
									overflow: hidden;
									position: absolute;
									top: 100%;
									left: -1px;
									right: -39px;
									border: 1px solid $colorBlack;
									background: $colorLightestGray;
									height: 67px;
									padding: 5px;

									.gif-item {
										width: auto;
										height: 55px;

										.btn-gif-select {
											position: relative;
											display: block;

											&.is-active {

												&:after {
													z-index: 1;
													content: '';
													position: absolute;
													top: 0;
													left: 0;
													display: block;
													width: 100%;
													height: 100%;
													border: 1px solid $colorBlack;
												}
											}
										}

										.gif-wrap {
											display: block;
											width: auto;
											height: 55px;

											img {
												display: block;
												height: 100%;
											}
										}
									}

									&.swiper-container-horizontal {
										.btn-gif-swiper {
											display: block;
										}
									}
								}

								&.is-gif-open {
									.gif-list-box {
										z-index: 2;
										display: block;
									}

									.btn-gif-open {
										color: #fff;
										background: $colorBlack;
									}
								}

								&.is-active {
									.comment-input {
										.ip-byte-check {
											.byte-check-textarea {
												padding: 10px 16px 10px 9px;
												height: 72px;
												transition: height .1s ease-in-out;
											}

											.byte {
												position: absolute;
												right: 9px;
												bottom: 0;
												padding: 0;
												height: 19px;

											}

											.btn-gif-open {
												margin-top: 8px;
											}
										}
									}

									.btn-write {
										height: 72px;
										transition: all .1s ease-in-out;
									}
								}
							}

							.comment-title {
								margin: 10px 0 7px;
								font-size: $bodyText5;
								line-height: 1.8rem;
								text-transform: uppercase;
							}

							.comment-list {
								border-top: 1px solid $colorLightestGray;

								.comment-item {
									padding: 10px 0 11px;
									border-bottom: 1px solid $colorLightestGray;

									.fold-box-header {
										display: block;
										padding-left: 30px;
										font-size: $bodyText5;
										font-family: $fontMix;
										line-height: 2rem;
										letter-spacing: -0.06rem;
										width: 100%;
										text-align: left;
										cursor: pointer;
									}

									.profile-flex-wrap {
										.profile-right {
											button {
												~ button {
													margin-left: 5px;
												}
											}
										}
									}


									.comment-text {
										display: block;
										margin-top: 4px;
										@include ellipsis;
										line-height: 2rem;
									}

									.comment-gif {
										display: none;
										padding-top: 12px;

										.ratio-img-wrap {
											display: block;
											width: 116px;

											.img-viewer {
												padding-top: 0;
												background-color: transparent;

												img {
													display: block;
													width: 100%;
												}
											}
										}
									}

									.comment-write-box {
										margin: {
											top: 10px;
											bottom: 1px;
										}

										.btn-gif-swiper {
											font-size: 0;
										}

										.btn-gif-delete {

										}
									}


									&.expanded {
										.fold-box-header {
											.comment-text {
												height: auto;
												max-height: none;
												text-overflow: initial;
												white-space: inherit;
											}

											.comment-gif {
												display: block;
											}
										}
									}

									.comment-write-box {
										display: none;
									}

									&.is-mod {
										.fold-box-header {
											display: none;
										}

										.comment-write-box {
											display: block;
										}
									}
								}
							}

							.no-data {
								border: {
									top: 1px solid $colorLightestGray;
									bottom: 1px solid $colorLightestGray;
								}
							}
						}

						// e: comment

						.tab-wrap {
							margin-top: 40px;

							.swiper-tab-box {
								position: -webkit-sticky;
								position: sticky;
								top: 0;
								z-index: 2;
								border-top: 1px solid $colorBlack;
							}

							+ .gate-banner-wrap {
								margin-top: 33px;

								.btn-swiper-arrow-wrap {
									display: block;
								}
							}
						}

						.banner-view-wrap {
							img {
								max-width: 100%;
							}
						}
					}
				}
			}
		}
	}
}

@import 'contents_detail';