@import 'scss_header';

.os-android, .os-apple {
	.wrap {
		min-height: calc(var(--vh, 1vh));

		.header-wrap {
			.header-box {
				height: calc(constant(safe-area-inset-top) + #{$headerHeightDefault});
				height: calc(env(safe-area-inset-top) + #{$headerHeightDefault});
				padding-top: constant(safe-area-inset-top);
				padding-top: env(safe-area-inset-top);
			}

			.mheader-box {
				height: calc(constant(safe-area-inset-top) + #{$headerHeightMain});
				height: calc(env(safe-area-inset-top) + #{$headerHeightMain});
				padding-top: constant(safe-area-inset-top);
				padding-top: env(safe-area-inset-top);
			}

			&.no-util-btn {
				~ .contents-wrap { 
					min-height: calc(var(--vh, 1vh) - 288px);
				}
			}
		}

		.bottom-fix-nav {
			@at-root .wrap.has-bottom & {
				-webkit-transition: bottom $transDefaultTime $transDefaultEase;
				transition: bottom $transDefaultTime $transDefaultEase;
				transform: none;
				-webkit-transform: none;
				-moz-transform: none;
				bottom: 0;

				&.scroll-down {
					bottom: -#{ constant(safe-area-inset-bottom) + $dockerHeightDefault };
					bottom: -#{ env(safe-area-inset-bottom) + $dockerHeightDefault };
				}
			}

			.docker-box {
				padding-bottom: calc(constant(safe-area-inset-bottom) + 10px);
				padding-bottom: calc(env(safe-area-inset-bottom) + 10px);
			}

			.btn-arrow-top, .btn-arrow-back {
				top: -#{constant(safe-area-inset-bottom) + 36px};
				top: -#{env(safe-area-inset-bottom) + 36px};
			}

			.prod-control-fix-wrap {
				top: -#{constant(safe-area-inset-bottom) + 55px};
				top: -#{env(safe-area-inset-bottom) + 55px};
			}

			.prd-option-wrap {
				padding-bottom: constant(safe-area-inset-bottom);
				padding-bottom: env(safe-area-inset-bottom);
			}
			.order-wrap {
				padding-bottom: calc(constant(safe-area-inset-bottom) + 10px);
				padding-bottom: calc(env(safe-area-inset-bottom) + 10px);
			}
		}

		.wrap-inner {
			.mheader-wrap ~ .contents-wrap {
				padding-top: calc(constant(safe-area-inset-top) + #{$headerHeightMain - 1});
				padding-top: calc(env(safe-area-inset-top) + #{$headerHeightMain - 1});
			}
		}

		.contents-wrap {
			min-height: calc(var(--vh, 1vh) - 282px);

			padding-top: calc(constant(safe-area-inset-top) + #{$headerHeightDefault});
			padding-top: calc(env(safe-area-inset-top) + #{$headerHeightDefault});

			@at-root .wrap.has-footer-padding .wrap-inner & {
				min-height: calc(var(--vh, 1vh) - 366px);
			}
			.no-data {
				&.full {
					height: calc( var(--vh, 1vh) - #{$headerHeightDefault} - 60px - 29px);

					&.no-bottom-fix-type {
						height: calc( var(--vh, 1vh) - #{$headerHeightDefault} - 29px);
					}
				}
			}
			&.no-bottom {
				min-height: var(--vh, 1vh); 

				.no-data.full.no-bottom-fix-type {
					height: calc(var(--vh, 1vh) - #{$headerHeightDefault + $dockerHeightDefault });
				}
			}

			.brand-filter-list-wrap .anchor-list-wrap.is-fixed {
				height: calc(var(--reVh, 1vh) - 45px) !important;
				height: calc(var(--reVh, 1vh) - 45px - constant(safe-area-inset-top)) !important;
				height: calc(var(--reVh, 1vh) - 45px - env(safe-area-inset-top)) !important;
			}
		}

		.main-search-wrap {
			height: calc(var(--vh, 1vh) - #{$headerHeightDefault + $dockerHeightDefault - 1});
			@include device_horizontal {
				min-height: calc(var(--vh, 1vh) - #{$headerHeightDefault + $dockerHeightDefault + 29 });
			}
		}

		.footer-wrap {
			padding-bottom: calc(constant(safe-area-inset-bottom) + 20px);
			padding-bottom: calc(env(safe-area-inset-bottom) + 20px);
		}

		&.has-footer-padding {
			.bottom-fix-nav ~ .footer-wrap {
				padding-bottom: calc(constant(safe-area-inset-bottom) + 75px);
				padding-bottom: calc(env(safe-area-inset-bottom) + 75px);
			}
		}
	}

	.ui-dialog {
		&.dialog-bottom {
			max-height: calc(var(--reVh, 1vh) - #{$headerHeightDefault -1 }) !important;

			.ui-dialog-contents {
				max-height: calc(var(--reVh, 1vh) - #{$headerHeightDefault}) !important;
				padding-bottom: constant(safe-area-inset-bottom);
				padding-bottom: env(safe-area-inset-bottom);

				.dialog-contents {
					max-height: calc(var(--reVh, 1vh) - #{$headerHeightDefault + 40});

					.filter-wrap {
						height: calc(var(--reVh, 1vh) - 40px - 45px - 65px);
					}
				}
			}
		}

		&.dialog-full,
		&.dialog-full-contentdetail {
			.ui-dialog-contents {
				height: calc(var(--reVh, 1vh) - 10px) !important;
			}
		}

		.option-select-box {
			max-height: calc(var(--reVh, 1vh) - #{$headerHeightDefault} - 51px - 65px - 15px);
		}

		$detailBottomPadding: 98px;

		.detail-fix-img-wrap {
			padding-top: calc( var(--reVh, 1vh) - #{ $headerHeightDetail + $detailBottomPadding });

			.fixed-img-wrap {
				// height: calc( var(--reVh, 1vh) - #{ $headerHeightDetail + $detailBottomPadding });

				height: calc( var(--reVh, 1vh) - #{ $headerHeightDetail + $detailBottomPadding } - constant(safe-area-inset-top));
				height: calc( var(--reVh, 1vh) - #{ $headerHeightDetail + $detailBottomPadding } - env(safe-area-inset-top));
			}

			.tagged-prod-wrap {
				min-height: calc( var(--reVh, 1vh) - 54px );
			}
		}

		&.dialog-tagged-prod {
			max-height: calc(var(--vh, 1vh) - 220px) !important;

			@include device_horizontal {
				max-height: calc(var(--vh, 1vh) - 45px) !important;
			}

			.dialog-contents {
				max-height: calc(var(--vh, 1vh) - 265px) !important;

				@include device_horizontal {
					max-height: calc(var(--vh, 1vh) - 90px) !important;
				}
			}
		}
	}

	.error-container {
		height: var(--vh, 1vh);
	}

	body.dialog-open {
		> .ui-dialog-container {
			//height: var(--reVh, 1vh);
			height: 100%;
			&.full {
				.ui-dialog {
					position: fixed;
					top: 10px;
					left: 0;
					right: 0;
					margin-top: 0;
					height: calc( 100% - 10px );
					padding-top: calc(constant(safe-area-inset-top));
					padding-top: calc(env(safe-area-inset-top));
				}
			}
		}
	}
}
