@import 'scss_header';

.wrap {
	.contents-wrap {
		.cs-center-info {
			margin-top: 30px;

			.sctitle {
				margin: 0;
				line-height: 1.6rem;
			}

			.cs-number {
				display: block;
				margin-top: 3px;
				font-size: 3rem;
				line-height: 3.8rem;
			}

			.cs-info {
				margin-top: 10px;
				font-size: $bodyText5;
				line-height: 2rem;
				text-transform: uppercase;
			}

			.cs-info-mail {
				display: block;
				margin-top: 11px;
				font-size: $bodyText5;
				line-height: 1.5rem;
			}

			+ .btn-even-wrap {
				margin-top: 30px;
			}
		}

		.cs-center-info-sm {
			margin-top: 20px;
			padding-bottom: 10px;

			.cs-number {
				display: block;
				font-size: $bodyText5;
				line-height: 1.8rem;
			}

			.cs-info {
				display: block;
				margin-top: 2px;
				font-size: $bodyText6;
				font-weight: 300;
				line-height: 1.7rem;
			}
		}

		.customer-quick-link {
			@include justify_width;
			flex-wrap: wrap;
			margin-top: 30px;
			border: 1px solid $colorBlack;
			border-width: 1px 1px 0 0;

			li {
				flex: 0 1 50%;
				border: 1px solid $colorBlack;
				border-width: 0 0 1px 1px;

				a {
					display: block;
					font-size: $bodyText6;
					padding: 0 15px;
					line-height: 38px;
				}

			}
		}

		.alliance-info-wrap {
			margin-top: 40px;
			text-align: center;

			.alliance-desc-box {
				.desc {
					font-size: $bodyText5;
					line-height: 1.8rem;
				}

				.email {
					margin-top: 15px;
					font-size: $bodyText2;
					font-weight: 500;
					line-height: 1.9rem;
				}
			}

			.alliance-list {
				margin-top: 30px;
				border: 1px solid $colorLightenGray;
				border-bottom: none;

				li {
					height: 39px;
					border-bottom: 1px solid $colorLightenGray;
					font-size: $bodyText6;
					line-height: 3.8rem;
				}
			}
		}

		.login-wrap {

			.login-desc {
				padding-top: 30px;
				font-size: $bodyText5;
				line-height: 1.8rem;
				text-align: center;
				margin-bottom: 5px;
			}

			.tab-content {
				padding-top: 15px;
			}

			.login-form {
				padding-top: 15px;

				.member-id {
					margin-top: 15px;
					margin-bottom: 12px;
					font-size: $bodyText3;
					line-height: 1.8rem;
					text-align: center;
				}

				.valid-check {
					&.login-id, &.login-pw {
						position: relative;

						.ui-input {
							padding-left: 39px;
						}

						&:before {
							content: '';
							position: absolute;
							display: block;
							width: 20px;
							height: 20px;
							top: 10px;
							left: 10px;
							z-index: 2;
							@include background_img("../images/customer/customer_icon_login_id.png", 40px, 40px);
						}
					}

					&.login-pw {
						&:before {
							@include background_img("../images/customer/customer_icon_login_pw.png", 40px, 40px);
						}
					}

					.valid-desc {
						margin-bottom: 10px;
					}
				}

				+ .btn-even-wrap {
					margin-top: 5px;
				}
			}

			.tbl-form {
				margin-top: 20px;

				+ .btn-even-wrap {
					margin-top: 10px;
				}
			}

			.captcha-wrap {
				padding: 15px 15px;
				margin-top: 10px;
				background-color: $colorLightestGray;

				.inner {
					max-width: 300px;
					margin: 0 auto;
				}

				.captcha-desc {
					margin-bottom: 10px;
					font-size: $bodyText6;
					line-height: 1.7rem;
				}

				.captcha-box {
					@include justify_width;

					.captcha-area {
						flex: 1;
						max-width: 205px;
						margin-right: 5px;
						height: 85px;
						overflow: hidden;

						img {
							height: 100%;
						}
					}

					.btn-wrap {
						flex: 0 0 90px;

						.btn {
							min-width: 90px;
							padding: 3px 10px;

							.ico {
								display: inline-block;
								width: 20px;
								height: 20px;
								margin-right: 4px;
								@include background_img("../images/customer/customer_icon_captcha.png", 80px, 40px);
								vertical-align: top;

								&.ico-speak {
									background-position: right 0;
								}
							}

							~ .btn {
								margin-top: 5px;
							}
						}
					}
				}

				.ui-input {
					margin-top: 8px;
				}

				+ .btn-even-wrap {
					margin-top: 10px;
				}
			}

			.login-option-wrap {
				margin-top: 13px;
				@include justify_width;
				align-items: center;

				.find-privacy-list {
					li {
						display: inline-block;
						vertical-align: top;

						~ li {
							margin-left: 15px;
						}
					}
				}
			}

			.sns-login-wrap {
				margin-top: 39px;
				@include justify_width;
				align-items: center;

				.text-list-title {
					margin: 0;
					text-transform: uppercase;
				}
			}

			.find-complete-desc {
				padding-top: 30px;
				font-size: $bodyText3;
				font-weight: 300;
				line-height: 2.2rem;
				text-align: center;

				.value {
					font-weight: 400;
				}

				+ .btn-even-wrap {
					margin-top: 30px;
				}
			}

			.join-guide-wrap {
				padding-top: 15px;

				.item {
					text-align: center;

					.join-title {
						display: block;
						padding-top: 15px;
						font-size: $bodyText3;
						line-height: 2rem;
					}

					.join-desc {
						margin-top: 10px;
						font-size: $bodyText5;
						font-weight: 300;
						line-height: 1.8rem;
						word-break: keep-all;
						word-wrap: break-word;
					}

					~ .item {
						margin-top: 30px;
						padding-top: 15px;
						border-top: 1px solid $colorLightenGray;
					}
				}

				+ .sns-login-wrap {
					margin-top: 20px;
				}
			}

			.guide-info-wrap,
			~ .guide-info-wrap {
				padding-top: 30px;
				padding-bottom: 30px;
				border-top: 1px solid $colorLightenGray;

				.info-desc {
					font-weight: 400;
				}
			}
		}

		.guide-info-wrap {
			.nonmember-order-link {
				margin-top: 10px;

				.btn-link {
					line-height: 1.7rem;
				}

				li {
					display: inline-block;
					vertical-align: top;

					~ li {
						margin-left: 15px;
					}
				}
			}
		}

		.pw-change-guide {
			margin: 15px 0 18px;

			.change-desc {
				font-size: $bodyText4;
				line-height: 2rem;
				word-break: keep-all;
				word-wrap: break-word;
			}

			.change-desc-sm {
				margin-top: 10px;
				font-size: $bodyText5;
				line-height: 2rem;
				color: $colorGray;
				word-break: keep-all;
				word-wrap: break-word;
			}
		}


	}
}

// 회원혜택 (비회원 주문시)
.join-benefit-box {
	margin-top: 30px;

	.item {
		text-align: center;

		.num {
			display: inline-block;
			font-size: 1.8rem;
			line-height: 2.3rem;
			text-decoration: underline;
		}

		.benefit-desc {
			margin-top: 5px;
			font-size: $bodyText4;
			line-height: 1.9rem;
		}

		~ .item {
			position: relative;
			margin-top: 20px;
			padding-top: 20px;

			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 25px;
				right: 25px;
				height: 1px;
				background-color: $colorLightestGray;
			}
		}
	}
}

// sns 간편가입
.sns-cert-box {
	@include justify_width(start);
	margin-bottom: 20px;
	padding: 20px 15px;
	background-color: $colorLightestGray;

	.ico {
		flex-shrink: 0;
		position: relative;
		display: inline-block;
		width: 50px;
		height: 50px;
		border: 1px solid $colorLightenGray;
		border-radius: 100%;
		background-color: #fff;
		vertical-align: top;

		&:after {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			display: block;
			width: 30px;
			height: 21px;
			@include background_img('../images/ui/button/ui_btn_sns_share.png', 300px, 42px);
			transform: translate(-50%, -50%);
		}

		&.naver {
			&:after {
				background-position: 0 0;
			}
		}

		&.facebook {
			&:after {
				background-position: -60px 0;
			}
		}

		&.kakao {
			&:after {
				background-position: -90px 0;
			}
		}
	}

	.sns-text {
		margin-left: 15px;

		.sns-type {
			display: block;
			font-size: $bodyText4;
			line-height: 1.9rem;
		}

		.sns-desc {
			display: block;
			margin-top: 4px;
			font-size: $bodyText5;
			font-weight: 300;
			line-height: 1.8rem;
			word-break: keep-all;
			word-wrap: break-word;
		}
	}
}

.privacy-validity-wrap {
	border-top: 1px solid $colorBlack;
	border-bottom: 1px solid $colorLightestGray;
	padding: 14px 0 20px;

	.desc-gray {
		margin-top: 9px;
	}
}


// s: 고객센터 메인
.cont-customer-main {
	.search-input-form {
		margin: {
			top: 30px;
			bottom: 30px;
		}
	}
}

// e: 고객센터 메인