@import 'scss_header';

.topbrand-wrap {
	.topbrand-banner-wrap {
		position: relative;
		margin: 0 -15px;
		padding: 0 15px;

		.topbrand-list {
			padding-bottom: 18px;

			.swiper-slide {
				width: 100px;

				.brand-link {
					display: block;

					.ratio-img-wrap {
						width: 100px;
						height: 100px;
						border-radius: 100%;

						.img-viewer {
							padding-top: 100%;
						}
					}

					.brand-name {
						margin-top: 7px;
						font-size: 1rem;
						font-weight: 700;
						@include multi_ellipsis(1.2rem);
						color: $colorBlack;
						text-align: center;
						word-break: break-word;
					}
				}

				&.swiper-slide-prev {
					transform: translateX(-5px);
				}
			}
		}

		&:after {
			content: '';
			display: block;
			position: absolute;
			left: 15px;
			right: 15px;
			bottom: 0;
			border-bottom: 1px solid $colorDarkGray;
		}
	}
}

.brand-filter-list-wrap {
	.brand-list-title {
		padding: 17px 0 0;
	}

	.filter-list-wrap {
		.filter-title {
			display: block;
			font-size: 1.6rem;
			line-height: 2rem;
			color: $colorBlack;
			padding-bottom: 10px;
			border-bottom: 1px solid $colorLightestGray;

			~ .filter-title {
				margin-top: 20px;
			}
		}

		.brand-list {
			li {
				height: 60px;
				border-bottom: 1px solid $colorLightestGray;

				.name-wrap {
					padding: 11px 15px 0 0;

					a {
						display: block;

						.inner {
							@include justify_width(start);

							.manual-badge-wrap {
								margin-left: 10px;
								flex-shrink: 0;

								.manual-badge {
									/*margin-left: 4px;*/

									.spot {
										color: $pointColor;
									}
								}
							}
						}

						.eng, .kor {
							@include ellipsis;
							display: block;
							width: auto;
							font-size: $bodyText5;
							line-height: 1.8rem;
						}

						.eng {
							font-weight: 700;
						}

						.kor {
							margin-top: 1px;
						}
					}
				}
			}

			&:last-child {
				li {
					&:last-child {
						height: auto;
						border-bottom: none;
					}
				}
			}
		}
	}

	.anchor-list-wrap {
		position: absolute;
		top: auto;
		bottom: 0;
		padding: 6px 0;

		.anchor-list {
			padding: 0;
		}

		&.is-fixed {
			position: fixed;
			top: 45px;
			top: calc(constant(safe-area-inset-top) + 45px);
			top: calc(env(safe-area-inset-top) + 45px);

			bottom: auto;
			height: calc(100vh - 45px) !important;
			height: calc(100vh - 45px - constant(safe-area-inset-top)) !important;
			height: calc(100vh - 45px - env(safe-area-inset-top)) !important;
		}
	}
}

.brand-detail-wrap {
	.profile-flex-wrap {
		padding: 10px 15px;
		border-bottom: 1px solid $colorLightestGray;
	}

	.more-text-wrap {
		margin-top: 15px;
	}

	.gate-detail-banner-wrap {
		margin-top: 17px;

		.gate-detail-banner {
			.swiper-slide {
				border-top: 1px solid $colorBlack;
			}
		}
	}
}

//브랜드 상세, event 상단 배너
.gate-detail-banner-wrap {
	.gate-detail-banner {
		.swiper-slide {
			width: 100%;
			border-bottom: 1px solid $colorBlack;

			.event-link, .video-box {
				position: relative;
				display: block;
				width: 100%;
			}

			.ratio-img-wrap {
				height: 168px;

				.img-viewer {
					height: 100%;
					padding-top: 0;
				}
			}

			.banner-info-wrap {
				height: 40px;
				padding: 0 15px;
				border-top: 1px solid $colorBlack;
				@include justify_width(center);
				align-items: center;

				.banner-type, .banner-text {
					display: inline-block;
					font-size: $bodyText4;
					vertical-align: top;

					span {
						display: block;
						line-height: $bodyText4;
					}
				}

				.banner-type {
					position: relative;
					padding-right: 12px;
					text-transform: uppercase;

					&:after {
						content: '/';
						position: absolute;
						width: 12px;
						text-align: center;
						top: 0;
						right: 0;
					}
				}

				.banner-text {
					font-family: $fontMix;
					width: auto;
					overflow: hidden;
					flex: 0 1 auto;

					span {
						@include ellipsis;
						display: block;
						line-height: $bodyText4;
					}
				}
			}

			.event-text {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
				right: 0;
				z-index: 2;
				text-align: center;

				span {
					display: block;
					font-size: 2.5rem;
					line-height: 2.6rem;
				}
			}
			&.swiper-slide-next {
				pointer-events: none;
			}
		}
	}

	&.js-gate-detail-banner {
		.gate-detail-banner {
			.swiper-slide {
				width: calc(100% - 60px);
				border-right: 1px solid $colorBlack;

				.detail-cont-box, .event-link, .video-box {
					&:after {
						content: '';
						position: absolute;
						width: 100%;
						height: 100%;
						background: rgba($colorBlack, .5);
						top: 0;
						z-index: 3;
						right: 0;
						transition: background .23s ease-in-out;
						-webkit-transition: background .23s ease-in-out;
					}
				}

				&.swiper-slide-active {
					.detail-cont-box, .event-link, .video-box {
						&:after {
							content: '';
							background: transparent;
						}
					}

					.detail-cont-box {
						.btn-gate-detail {
							z-index: 4;
						}
					}
				}
			}
		}
	}
}

.brand-lookbook-list {
	li {
		.ratio-img-wrap {
			border-bottom: 1px solid $colorBlack;

			.img-viewer {
				padding-top: 66.6666%;
			}
		}

		.lookbook-title {
			display: block;
			margin: 13px 15px 26px;
			font-size: $bodyText2;
			letter-spacing: -0.038rem;
			font-weight: 500;
			@include multi_ellipsis(2.5rem);
			word-break: break-word;
		}

		.profile-name {
			padding: 10px 15px;
			border-top: 1px solid $colorLightestGray;
		}

		.no-data {
			&:not(.full) {
				padding-bottom: 50px;
			}
		}

		~ li {
			.ratio-img-wrap {
				border-top: 1px solid $colorBlack;
			}
		}
		&:last-child {
			.detail-cont-box {
				margin-bottom: -30px;
			}
		}
	}
}

.focus-banner-wrap {
	border-bottom: 1px solid $colorBlack;

	.video-box, .focus-link, .detail-cont-box {
		display: block;
		width: 100%;

		.ratio-img-wrap {
			height: 480px;
			border-bottom: 1px solid $colorBlack;

			.img-viewer {
				height: 100%;
				padding-top: 0;
			}
		}
	}

	.focus-text {
		display: block;
		margin: 13px 15px 26px;
		font-size: $bodyText2;
		letter-spacing: -0.038rem;
		font-weight: 500;
		word-break: break-word;
		color: $colorBlack !important;

		span {
			display: block;
			line-height: 2.5rem;
		}
	}

	.profile-name {
		padding: 10px 15px;
		border-top: 1px solid $colorLightestGray;
	}
}

@media screen and (min-width: 700px) {
	.gate-detail-banner-wrap {
		.gate-detail-banner {
			.swiper-slide {
				/* TODO : 700 이상인 경우, 이미지 height 조절 필요  */
				.ratio-img-wrap {

				}
			}
		}
	}

	.focus-banner-wrap {
		.video-box, .focus-link, .detail-cont-box {
			.ratio-img-wrap {

			}
		}
	}
}