@import 'scss_header';

.wrap {
	.contents-wrap {
		// 주문 금액
		.order-price-wrap {
			margin-bottom: 40px;

			li {
				@include justify_width;
				font-size: $bodyText4;
				color: $colorDarkGray;
				line-height: 1.9rem;

				.order-title {
					display: block;
					width: 130px;
					padding-right: 10px;
					letter-spacing: -0.065rem;
				}

				.value {
					width: calc(100% - 130px);
					display: block;
					text-align: right;
				}

				~ li {
					margin-top: 15px;
				}

				&.total {
					color: $colorBlack;
					margin-top: 20px;
					font-size: 1.7rem;
					font-weight: 500;
					line-height: 2.6rem;

					.order-title {
						letter-spacing: -0.085rem;
					}
				}
			}

			// s: 주문 최종 금액 ( 주문서 )
			&.final {
				padding: 20px 0;
				margin: 0;
				border-bottom: 1px solid $colorBlack;
			}

			// e: 주문 최종 금액 ( 주문서 )
		}

	}
}

// s: 결제정보
.payment-info-wrap {
	border-top: 1px solid $colorBlack;

	.payment-type {
		display: block;
		padding-top: 15px;
		font-size: $bodyText5;
		font-weight: 500;
		line-height: 2rem;
		color: $colorBlack;
	}

	.receipt-list {
		margin-top: 8px;
		padding-bottom: 12px;
		border-bottom: 1px solid $colorLightestGray;

		li {
			@include justify_width;

			.receipt-title, .value {
				display: block;
				font-size: $bodyText5;
				color: $colorGray;
				line-height: 2rem;
			}

			.receipt-title {
				width: 105px;
				padding-right: 10px;

				~ .value {
					width: calc(100% - 105px);
				}
			}

			.value {

				margin-left: auto;
				text-align: right;
			}

			~ li {
				margin-top: 2px;
			}
		}
	}

	.total {
		@include justify_width;
		flex-wrap: wrap;
		align-items: center;
		padding: 20px 0;
		border-bottom: 1px solid $colorLightestGray;

		.payment-type {
			display: block;
			width: 100px;
			padding-top: 0;
			padding-right: 10px;
		}

		.total-price {
			display: block;
			width: calc(100% - 100px);
			margin-left: auto;
			font-size: $bodyText3;
			font-weight: 500;
			color: $colorBlack;
			line-height: 2rem;
			text-align: right;
		}

		.payment-history {
			width: 100%;
			margin-top: 6px;
			font-size: $bodyText5;
			line-height: 1.6rem;
			color: $colorGray;
			text-align: right;
		}
	}

	.btn-even-wrap {
		margin-top: -1px;
		background: #fff;
	}
}

// e: 결제정보
// s : 개인통관고유부호 발급안내 팝업
.unique-code-guide {
	.desc {
		font-size: $bodyText4;
		line-height: 2.2rem;
	}

	.guide-list-title {
		display: block;
		margin-top: 20px;
		padding-top: 21px;
		border-top: 1px solid $colorLightestGray;
		font-size: $bodyText5;
		line-height: 2.5rem;
	}

	.guide-list {
		margin-top: 8px;

		li {
			color: $colorLightGray;
			font-size: $bodyText6;
			line-height: 1.6rem;

			a {
				color: $colorBlack;
			}

			+ li {
				margin-top: 6px;
			}
		}
	}

	@at-root .dialog-contents & {
		padding: 15px;
	}
}

// e : 개인통관고유부호 발급안내 팝업

// s : 무이자 할부혜택
.interest-list {
	border-top: 1px solid $colorBlack;
	li {
		@include justify_width;
		padding: {
			top: 16px;
			bottom: 15px;
		}
		border-bottom: 1px solid $colorLightestGray;
		align-items: center;
		word-break: keep-all;
		word-wrap: break-word;

		.interest-title {
			width: 105px;
			font-size: $bodyText5;
			line-height: 1.8rem;
		}

		.interest-detail {
			flex: 1;
			font-size: $bodyText5;
			line-height: 1.8rem;
		}
	}

	+ .text-list {
		margin-top: 20px;
	}

}
.tbl-form {
	.form-cont {
		.interest-list {
			margin: 10px 0 20px;
		}
	}
}

// e : 무이자 할부혜택

// s: 비회원 가이드
.nonmember-guide-wrap {
	padding: 15px 0;
	@include justify_width;
	align-items: center;

	.login-desc {
		flex: 1;
		margin-right: 15px;
		font-size: 1.1rem;
		line-height: 1.8rem;
		word-break: keep-all;
		word-wrap: break-word;
	}

	.btn {
		min-width: 64px;
		width: 64px;
		text-transform: uppercase;
	}
}

// e: 비회원 가이드

// s: 최초 주문 본인인증
.identity-cert-wrap {
	padding: 15px 0 25px;
	text-align: center;

	.cert-desc {
		font-size: 1.2rem;
		line-height: 1.8rem;
	}

	.btn {
		margin-top: 15px;
		min-width: 180px;
	}
}

// e: 최초 주문 본인인증

.contents-box-wrap {
	.content-box {
		.tbl-form {
			padding-bottom: 20px;
		}

		.term-fold-wrap {
			border: 0;
			padding: 15px 0 20px;

			&:not(:first-child) {
				padding-top: 0;
			}

			.text-list-title {
				margin-bottom: 10px;

				&:first-child {
					margin-top: 20px;
				}
			}

			.fold-box-list {
				border: none;

				.fold-box {
					border: 1px solid $colorLightenGray;

					.fold-box-header {
						.toggle-title-box {
							border-bottom: 0;
						}
					}

					~ .fold-box {
						margin-top: 10px;
					}

					&.expanded {
						.fold-box-contents {
							max-height: 90px;
							border-top: 1px solid $colorLightenGray;
							border-bottom: 0;
						}
					}
				}
			}
		}
	}
}

// s : 약관 폴딩 박스

// 일반 결제 상세
.common-payment-detail {
	padding-top: 5px;

	.text-list-title {
		line-height: 2rem;
	}

	.text-list {
		margin-top: 8px !important;

		.star-text {
			+ .star-text {
				margin-top: 6px;
			}
		}
	}

	.payment-info-btn-wrap {
		padding-top: 5px;
		text-align: center;
	}

	.setting-chk-box {
		margin-top: 15px;
		padding-top: 10px;
		border-top: solid 1px $colorLightestGray;
	}
}


// s: 결제 승인 실패
.order-fail-wrap {
	padding-top: 30px;
	text-align: center;
	word-break: keep-all;
	word-wrap: break-word;

	.fail-spot-desc {
		margin-bottom: 10px;
		font-size: 1.6rem;
		line-height: 2.4rem;
	}

	.fail-desc {
		margin-bottom: 15px;
		font-size: $bodyText5;
		line-height: 1.8rem;
		color: $colorDarkGray;
		font-weight: 300;
	}

	.fail-sdesc {
		margin-top: 5px;
		font-size: $bodyText6;
		line-height: 1.8rem;
		color: $colorLightGray;
	}
}

// e: 결제 승인 실패