@import 'scss_header';

.wrap {
	.contents-padding {
		padding: {
			left: 15px;
			right: 15px;
		}
	}

	/* s: bottom-fix-nav */
	.bottom-fix-nav {
		position: fixed;
		width: 100%;
		left: 0;
		z-index: 200;
		bottom: 0;

		.btn-arrow-top {
			opacity: 0;

			&.is-active {
				opacity: 1;
				-webkit-transition: opacity $transDefaultTime $transDefaultEase;
				transition: opacity $transDefaultTime $transDefaultEase;
				-webkit-backface-visibility: hidden;
			}
		}

		.docker-box {
			background: #fff;
			opacity: 1;
		}
		&.scroll-down {
			.docker-box {
				opacity: 0;
			}
		}
		&.detail-bottom-fix-nav {
			background:#fff;
		}
	}

	/* e: bottom-fix-nav */

}


/*/Android/i.test(navigator.userAgent);*/
.contents-wrap {
	position: relative;
	min-height: calc(100vh - 288px);
	padding-top: $headerHeightDefault;
	padding-top: calc(constant(safe-area-inset-top) + #{$headerHeightDefault});
	padding-top: calc(env(safe-area-inset-top) + #{$headerHeightDefault});
	padding-bottom: 30px;

	.contents-box-wrap {
		margin-bottom: -1px;
		background-color: $colorLightestGray;

		.content-box {
			.toggle-title-box {
				min-height: 45px;

				.toggle-title {
					flex: 1;
				}

				.ico-toggle {
					margin-left: 15px;
				}
			}

			margin-top: 15px;
			background: #fff;
			border: {
				top: 1px solid $colorBlack;
				bottom: 1px solid $colorLightenGray;
			}

			.tab-wrap {
				margin-top: -1px;
				border-top: 1px solid $colorLightenGray;
			}

			&:first-child {
				border-top: 0;
				margin-top: 0;
			}

			&.fold-box {
				> .fold-box-header {
					.toggle-title-box {
						border-bottom-color: transparent;
					}
				}

				> .fold-box-contents {
					display: none;
				}

				&.expanded {
					> .fold-box-header {
						.toggle-title-box {
							border-bottom: 1px solid $colorLightestGray;
						}
					}

					> .fold-box-contents {
						display: block;
					}
				}
			}
		}

		&:last-child {
			padding-bottom: 15px;
		} 
	}

	.cont-padding {
		padding: {
			left: 15px;
			right: 15px;
		}
	}

	.content-gray-box {
		background-color: $colorLightestGray;
	}

	@at-root .wrap .wrap-inner .mheader-wrap ~ & {
		min-height: auto;
		padding: {
			top: #{$headerHeightMain - 1};
			bottom: 0;
		}

		padding-top: calc(constant(safe-area-inset-top) + #{$headerHeightMain - 1});
		padding-top: calc(env(safe-area-inset-top) + #{$headerHeightMain - 1});
	}

	@at-root .wrap.has-footer-padding .wrap-inner & {
		min-height: calc(100vh - 366px);
	}

	&.no-bottom {
		min-height: 100vh;
		padding-bottom: $dockerHeightDefault;
		padding-bottom: calc(constant(safe-area-inset-bottom) + #{$dockerHeightDefault});
		padding-bottom: calc(env(safe-area-inset-bottom) + #{$dockerHeightDefault});		

		.no-data.full.no-bottom-fix-type {
			padding-top: 60px;
			height: calc(100vh - #{$headerHeightDefault + $dockerHeightDefault });
		}
	}

	&.no-header-bottom {
		padding: 0;
	}
}