@import 'scss_header';


.wrap {
	.fix-val {
		z-index: 9999;
		position: fixed;
		top: 50%;
		margin-top: -10px;
		font-size: 1.8rem;
		left: 0;
		line-height: 2rem;
	}
}

/* s: search */
.main-search-wrap {
	margin-bottom: -30px;
	height: calc(100vh - #{$headerHeightDefault + $dockerHeightDefault - 1 });

	@include device_horizontal {
		height: auto;
		min-height: calc(100vh - #{$headerHeightDefault + $dockerHeightDefault + 28 });
		.search-swiper-wrap {
			.search-item {
				height: 459px;
			}
		}
	}

	.search-swiper-wrap {
		height: calc(100% - 45px);
		/*height: 100%;*/
		padding: 30px 55px 0;


		.search-item {
			.btn-data-delete {
				margin: 10px auto 0;
				padding: 10px 30px;
				display: block;
				font-size: $bodyText5;
				font-weight: 300;
				line-height: 1.8rem;
				text-align: center;
			}

			.search-sec-box {
				height: calc(100% - 70px);

				@include mob_small {
					height: calc(100% - 60px);
				}
				box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
				background-color: #fff;

				.search-title-flex {
					@include ellipsis;
					padding: 11px 15px;
					font-size: 1.2rem;
					line-height: 1.8rem;
					text-transform: uppercase;
				}

				.search-content-flex {
					overflow-y: hidden;
					padding: 10px 0 0;
					height: calc(100% - 40px);
					border-top: solid 1px $colorLightestGray;

					.cont-padding {
						/*height: 100%;*/

						.no-data {
							height: 100%;
							@include justify_width;
							justify-content: center;
							-webkit-justify-content: center;
							align-items: center;
							-webkit-align-items: center;
							-ms-flex-align: center;
						}
					}

					.word-list {
						li {
							padding: 10px 0 11px;
							@include justify_width;

							&:last-child {
								padding-bottom: 21px;
							}
						}

						.btn-word {
							@include ellipsis;
							flex: 1;
							display: block;
							font-size: 1.3rem;
							font-weight: 300;
							line-height: 2rem;

							.num {
								width: 14px;
								display: inline-block;
								font-size: 1.4rem;
								margin-right: 14px;
							}
						}

						.btn-word-delete {
							width: 20px;
							height: 20px;
							@include background_img('../images/common/comm_search_btn_delete.png', 40px, 40px);
						}
					}

					.flex-box-list-wrap {
						padding: 0 15px 10px;

						.thumb-item-box {
							.ratio-img-wrap {
								.img-text-wrap {
									bottom: 10px;
									left: 10px;
									right: 10px;

									.editor-wrap {
										height: 20px;
										line-height: 2rem;
										font-size: 0.9rem;

										~ .editor-wrap {
											margin-top: 2px;
										}
									}
								}
							}
						}
					}
				}
			}

			&.swiper-slide-active {
				.search-content-flex {
					overflow-y: auto;
				}
			}
		}
	}


}

.content-search-wrap {
	margin-bottom: -30px;

	.stitle {
		padding-top: 30px;
		margin-bottom: 0;
	}

	.no-data {
		padding-bottom: 50px;

		.spot {
			font-weight: 400;
		}
	}

	.prod-grid-list {
		padding-bottom: 30px;

		&.popular-prd-type {
			padding: {
				top: 0;
				bottom: 0;
			}
		}
	}

	.box-list-wrap {
		&.brand-type {
			padding-bottom: 30px;
		}

	}

}

/* s: search */
