@import 'scss_header';
@import 'contents_search';
@import 'contents_brand';
@import 'contents_gate';
@import 'contents_device';
@import 'contents_customer';
@import 'contents_order';
@import 'contents_mypage';
@import 'contents_equal';
@import 'contents_main';

.spot {
	font-weight: 700;
}

/* s: bottom-fix-nav */
.bottom-fix-nav {

	@at-root .wrap.has-bottom & {
		-webkit-transition: transform $transDefaultTime $transDefaultEase;
		transition: transform $transDefaultTime $transDefaultEase;
		-webkit-backface-visibility: hidden;
		transform: translate(0, 0);
		-webkit-transform: translate(0, 0);
		-moz-transform: translate(0, 0);
		transform-style: preserve-3d;
		-webkit-transform-style: preserve-3d;

		&.scroll-down {
			transform: translate(0, $dockerHeightDefault);
			-webkit-transform: translate(0, $dockerHeightDefault);
			-moz-transform: translate(0, $dockerHeightDefault);

			.docker-box {
				[class*=btn-] {
					opacity: 0;
					transform: translateY(10px);
					-webkit-transition: none;
					transition: none;
				}
			}
		}

		.prod-control-fix-wrap {
			-webkit-transition: opacity $transDefaultTime $transDefaultEase;
			transition: opacity $transDefaultTime $transDefaultEase;
		}

	}
	.docker-box {
		border-top: 1px solid $colorBlack;
		padding: 10px 7px;
		@include justify_width;
		text-transform: uppercase;


		[class*=btn-] {
			display: block;
			text-align: center;
			margin: 0 auto;
			opacity: 1;
			transform: translateY(0);
			-webkit-transition: transform $transDefaultTime $transDefaultEase #{$transDefaultTime/2}, opacity $transDefaultTime $transDefaultEase #{$transDefaultTime/2};
			transition: transform $transDefaultTime $transDefaultEase #{$transDefaultTime/2}, opacity $transDefaultTime $transDefaultEase #{$transDefaultTime/2};
			-webkit-backface-visibility: hidden;


			i {
				margin: 0 auto;
			}
		}

		[class*=btn-]:not(.btn-home) {
			width: 36px;

			i {
				width: 30px;
				height: 30px;
				@include background_img('../images/common/comm_footer_docker_btn.png', 246px, 120px);
			}

			span {
				display: block;
				font-size: 0.8rem;
				color: $colorBlack;
				text-align: center;
			}


			&.btn-search i {
				background-position: -31px 0;
			}

			&.btn-equal i {
				background-position: -62px 0;
			}

			&.btn-mypage i {
				background-position: -93px 0;
			}

			&:hover, &:focus, &.active {
				&.btn-menu i {
					background-position: 0 -30px;
				}

				&.btn-search i {
					background-position: -31px -30px;
				}

				&.btn-equal i {
					background-position: -62px -30px;
				}

				&.btn-mypage i {
					background-position: -93px -30px;
				}
			}
		}

		.btn-home {
			flex: 0 1 80px;

			@include mob_small {
				margin: 0 15px;
			}

			i {
				width: 80px;
				height: 30px;
				@include background_img('../images/common/comm_footer_docker_btn_home.png', 160px, 60px);
			}

			span {
				@include offscreen;
			}
		}
	}


	/* top */
	.btn-arrow-top {
		position: absolute;
		top: -36px;
		right: 0;
	}

	/* back */
	.btn-arrow-back {
		position: absolute;
		top: -36px;
		left: 0;
	}


	/* s: 카테고리 */
	.prod-control-fix-wrap {
		position: absolute;
		top: -55px;
		left: 50%;
		width: 280px;
		margin-left: -140px;
		opacity: 0;

		@include mob_small {
			left: 45px;
			right: 45px;
			width: initial;
			margin-left: 0;
		}

		.category-btn-wrap {
			height: 40px;
			@include justify_width;
			flex-wrap: wrap;
			justify-content: center;
		}

		&.is-active {
			opacity: 1;
		}
	}

	.prd-option-wrap {
		.prd-buy {
			@include justify_width(start);
			padding: 10px 15px;
			border-top: 1px solid $colorBlack;

			.btn-left {
				width: 50%;
				padding: 0 1px;

				[class*=btn-item-] {
					~ [class*=btn-item-] {
						margin-left: 9px;
					}
				}

				~ .btn {
					width: 50%;
					min-width: 50%;
				}
			}

			.btn {
				min-width: 100%;
				color: #fff;
				font-size: $bodyText3;
			}
		}
	}

	.order-wrap {
		@include justify_width(start);
		align-items: center;
		padding: 10px 15px;
		border-top: 1px solid $colorBlack;

		.btn-left {
			width: 50%;
			padding: 0 1px;

			~ .btn {
				width: 50%;
				min-width: 50%;
			}

			.order-total-info {
				font-size: 1.2rem;
				line-height: 1.8rem;

				.price,
				.count {
					display: inline-block;
				}
			}
		}

		.btn {
			min-width: 100%;
			color: #fff;
			font-size: $bodyText3;
		}
	}
}

