// s: 카운트다운
.count-down-wrap {
	text-align: center;
	font-size: 0;
	font-family: $fontEng;

	.detail-stitle {
		text-transform: uppercase;
		font-size: $bodyText4;
	}

	.clock-wrap {
		padding-top: 12px;

		.count-wrap {
			position: relative;
			display: inline-block;
			width: 43px;
			text-align: center;
			color: $colorBlack;
			vertical-align: top;

			.count {
				font-size: 2.4rem;
				line-height: 3rem;
				padding-bottom: 2px;
			}

			.sstitle {
				font-size: 1rem;
				line-height: 1.3rem;
			}
		}

		.gap {
			display: inline-block;
			width: 13px;
			height: 32px;
			font-size: 2.4rem;
			text-align: center;
			vertical-align: top;
		}
	}
}

// e: 카운트다운


// offscreen
.offscreen {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	margin: -1px;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	border: 0;
	font-size: $bodyText6;
}


// s: gete menu
.gate-menu-list {
	border-top: solid 1px $colorBlack;

	.menu-item {
		border-bottom: solid 1px $colorBlack;

		.btn-menu {
			display: block;
			padding: 15px;
			font-size: 1.6rem;
			line-height: 2rem;
			text-align: center;

			.spot {
				font-weight: 400;
				color: $pointColor;
			}

			.spot2 {
				color: #016709;
			}
		}
	}
}

// e: gete menu
// s: 카테고리 메뉴
.category-menu-wrap {
	padding-top: 20px;
	border-top: 1px solid $colorBlack;

	.category-title {
		margin-bottom: 10px;
		font-size: 1.6rem;
		line-height: 2rem;

		a {
			display: block;
		}
	}

	.fold-box-list {
		border-top: 1px solid $colorLightestGray;

		.toggle-title-box {
			padding: {
				left: 0;
				right: 0;
			}
			border-bottom-color: $colorLightestGray;
			text-transform: uppercase;

			.toggle-title {
				font-size: 1.6rem;
				line-height: 2rem;
			}

			.right-box {
				.ico-toggle {
					margin-right: 10px;
					width: 9px;
					height: 9px;
					@include background_img('../images/ui/button/ui_btn_category_toggle.png', 36px, 18px);
				}
			}
		}

		.fold-box {
			&.expanded {
				.fold-box-contents {
					border-bottom-color: $colorLightestGray;
				}

				.toggle-title-box {
					.right-box {
						.ico-toggle {
							background-position: right 0;
						}
					}
				}
			}

			.fold-box-contents {
				padding: 0;

				.menu-list {
					.menu-item {

						.btn-menu {
							display: block;
							padding: 10px 20px;
							font-size: 1.6rem;
							font-weight: 300;
							line-height: 2rem;
							text-transform: uppercase;
						}

						~ .menu-item {
							border-top: 1px solid $colorLightestGray;
						}
					}
				}
			}
		}
	}

}

// e: 카테고리 메뉴

//s : 약관

.terms-wrap {
	.privacy-guide {
		margin: 0 -15px;
		padding: 30px 35px;
		background-color: #f2f2f2;
		text-align: center;
		word-break: keep-all;
		word-wrap: break-word;

		p {
			font-size: $bodyText5;
			line-height: 1.8rem;

			~ p {
				margin-top: 4px;
			}
		}

		+ .terms-list {
			margin-top: 30px;
		}
	}

	.term-date {
		display: block;
		margin-top: 30px;
		font-size: $bodyText5;
		line-height: 1.8rem;
		color: $colorGray;
	}

	.terms-list {
		margin-top: 15px;


		> li { // 1depth
			// s : 공통
			.term-title, .term-stitle {
				display: block;
				margin-bottom: 10px;
				font-family: $fontMix;
				font-size: $bodyText4;
				line-height: 1.9rem;

				+ .tbl-col {
					margin-top: 0;
				}
			}

			.term-tbl-title {
				display: block;
				font-size: $bodyText6;
				line-height: 1.8rem;
				margin-bottom: 10px;

				+ .tbl-col {
					margin-top: 0;
				}
			}

			.text-list-title {
				margin: 20px 0 8px;
				line-height: 1.8rem;
			}

			.desc-gray {
				display: block;
				margin-top: 10px;
			}

			p {
				font-size: $bodyText5;
				line-height: 1.8rem;
				color: $colorGray;
				word-break: keep-all;
				word-wrap: break-word;

				+ ol {
					margin-top: 10px;
				}

				> ul {
					margin-top: 4px;
				}
			}

			.tbl-col {
				margin-top: 20px;

				table {
					thead {
						th {
							word-break: keep-all;
							word-wrap: break-word;
						}
					}

					td {
						a {
							display: block;
							margin-top: 6px;
							color: $colorBlack;
							text-decoration: underline;
						}

						span {
							word-break: break-word;
						}

						ul {
							li {
								display: inherit;

								&:before {
									content: none;
								}

								~ li {
									margin-top: 6px;
								}
							}
						}
					}
				}
			}

			ul {
				width: 100%;

				li {
					@include justify_width(start);
					flex-wrap: wrap;

					&:before {
						content: '•';
						display: inline-block;
						margin-right: 5px;
						font-size: $bodyText6;
						line-height: 1.6rem;
						color: $colorGray;
						vertical-align: top;

					}

					p {
						&:first-child {
							flex: 1;

						}
					}
				}

				&.text-list {
					margin-top: 8px;

					li {
						&:before {
							content: none;
						}

						~ li {
							margin-top: 6px;
						}
					}
				}
			}

			ol {
				width: 100%;

				li {
					@include justify_width(start);
					flex-wrap: wrap;

					.num {
						display: inline-block;
						font-size: $bodyText5;
						vertical-align: top;
						color: $colorGray;
						line-height: 1.8rem;

						+ p {
							width: auto;
							flex: 1;

							+ p {
								width: 100%;
							}
						}
					}

					li {
						p {
							width: 100%;
							font-size: $bodyText6;
						}
					}
				}
			}

			// e : 공통

			> p {
				~ p {
					margin-top: 10px;
				}

				+ ul {
					margin-top: 6px;

					li {
						&:before {
							content: none;
						}
					}
				}
			}

			> ol {
				> li { //2depth
					> .num {
						padding-right: 3px;

						&.desc-black {
							+ p {
								+ p {
									margin-top: 6px;
								}
							}
						}
					}

					> p {
						~ p {
							margin: 4px 0 0 11px;
						}
					}

					> ol {
						width: 100%;
						margin: 8px 0 0 10px;

						li {
							> .term-title {
								margin-bottom: 10px;
							}
						}

						> li { // 3depth
							> p {
								~ p {
									margin: 4px 0 0 15px;
								}
							}

							> .term-stitle, > .term-tbl-title {
								margin-top: 15px;
							}

							&.fix-left {
								margin-left: -10px;
							}

							> ol, > ul {
								width: 100%;
								margin: 5px 0 0 10px;

								> li { // 4depth
									> p {
										~ p {
											margin: 4px 0 0 13px;
										}
									}

									> .term-stitle, > .term-tbl-title {
										margin-top: 17px;
									}

									&.fix-left {
										margin-left: -20px;
									}

									> ol, > ul {
										width: 100%;
										margin: 3px 0 0 10px;

										> li { // 5depth
											> p {
												~ p {
													margin: 2px 0 0 15px;
												}
											}

											> .term-stitle, > .term-tbl-title {
												margin-top: 17px;
											}

											&.fix-left {
												margin-left: -30px;
											}

											> ol, > ul {
												width: 100%;
												margin: 3px 0 0 15px;

												> li { // 6depth
													> .term-stitle, > .term-tbl-title {
														margin-top: 19px;
													}

													&.fix-left {
														margin-left: -45px;
													}

													~ li { // 6depth 사이간격
														margin-top: 1px;
													}
												}
											}

											~ li { // 5depth 사이간격
												margin-top: 3px;
											}
										}

									}

									~ li { // 4depth 사이간격
										margin-top: 3px;
									}
								}
							}

							&:first-child {
								.tbl-col {
									margin-top: 16px;
								}
							}

							~ li { // 3depth 사이간격
								margin-top: 5px;
							}
						}
					}

					li {
						> .num {
							padding-right: 5px;
						}
					}

					~ li { // 2depth 사이간격
						margin-top: 8px;
					}
				}
			}

			~ li {
				margin-top: 30px;
			}
		}
	}

	.terms-select-wrap {
		margin-top: 15px;
		padding-top: 15px;
		border-top: 1px solid $colorLightestGray;

		.select-title {
			display: block;
			font-size: $bodyText5;
			line-height: 1.8rem;
		}

		.ui-sel {
			margin-top: 8px;
		}
	}
}

//e : 약관


// s: about
.about-info-wrap {

	$transAboutTitTiming: ease;
	$transAboutTxtTiming: ease-in;

	margin-bottom: -30px;

	.about-visual {
		position: relative;
		border-bottom: 1px solid $colorBlack;

		.ratio-img-wrap {
			position: relative;

			img {
				max-width: 100%;
			}

			.img-viewer {
				padding-top: 168px;
			}
		}

		.visual-title-wrap {
			@include justify_width(center);
			align-items: center;
			-webkit-align-items: center;
			z-index: 5;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			text-align: center;

			.visual-title {
				position: relative;
				font-weight: 500;
				font-size: 2.5rem;
				line-height: 3rem;
				color: #fff;

				&:after {
					z-index: 0;
					position: absolute;
					bottom: 0;
					left: -5px;
					top: 0;
					opacity: 1;
					height: 3rem;
					width: calc(100% + 5px);
					content: '';
					display: block;
					background: #fff;
					border: 0;
					transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
					-webkit-transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
				}

				.tit {
					display: block;
					opacity: 0;
				}
			}
		}

		&.is-animate {
			.tit {
				animation: visualTitleText 0.8s forwards;
				-webkit-animation: visualTitleText 0.8s forwards;
			}

			.visual-title-wrap {
				.visual-title {
					&:after {
						animation: visualTitleBg 0.8s forwards;
						-webkit-animation: visualTitleBg 0.8s forwards;
					}
				}
			}
		}

		@keyframes visualTitleText {
			0% {
				opacity: 0;
			}
			61% {
				opacity: 0;
			}
			62% {
				opacity: 1;
			}
			100% {
				opacity: 1;
			}
		}
		@keyframes visualTitleBg {
			0% {
				transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
				-webkit-transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
				transform-origin: left;
				-webkit-transform-origin: left;
			}
			39% {
				transform: scale3d(0, 1, 1) translate3d(0, 0, 0);
				-webkit-transform: scale3d(0, 1, 1) translate3d(0, 0, 0);
				transform-origin: left;
				-webkit-transform-origin: left;
			}
			60% {
				transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
				-webkit-transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
				transform-origin: left;
				-webkit-transform-origin: left;
			}
			61% {
				transform-origin: right;
				-webkit-transform-origin: right;
			}
			62% {
				transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
				-webkit-transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
				transform-origin: right;
				-webkit-transform-origin: right;
			}
			100% {
				transform: scale3d(0, 1, 1) translate3d(0, 0, 0);
				-webkit-transform: scale3d(0, 1, 1) translate3d(0, 0, 0);
				transform-origin: right;
				-webkit-transform-origin: right;
			}
		}
	}

	.about-intro {
		.intro-item {
			position: relative;
			padding: 15px 15px 50px;

			.text {
				font-size: $bodyText4;
				line-height: 2.2rem;
				text-align: left;
				word-break: keep-all;
				word-wrap: break-word;
				opacity: 0;
				transform: translate3d(0, 25px, 0);
				-webkit-transform: translate3d(0, 25px, 0);
				-moz-transform: translate3d(0, 25px, 0);
				-ms-transform: translate3d(0, 25px, 0);
			}

			.comm-text {
				overflow: hidden;
				margin-top: 30px;
				font-size: $bodyText4;
				line-height: 1.7rem;
				text-align: left;
				word-break: keep-all;
				word-wrap: break-word;

				.text-inner {
					display: block;
					opacity: 0;
					transform: translate3d(0, 25px, 0);
					-webkit-transform: translate3d(0, 25px, 0);
					-moz-transform: translate3d(0, 25px, 0);
					-ms-transform: translate3d(0, 25px, 0);
				}
			}

			&:after {
				content: '';
				display: block;
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				height: 1px;
				background: $colorBlack;
				transform: scale3d(0, 1, 1);
				-webkit-transform: scale3d(0, 1, 1);
				-moz-transform: scale3d(0, 1, 1);
				-ms-transform: scale3d(0, 1, 1);
			}

			&:last-child {
				&:after {
					display: none;
				}
			}

			&.is-animate {
				&:first-child {
					.text {
						transition-delay: 0.9s;
					}

					.comm-text .text-inner {
						transition-delay: 1s;
					}

					&:after {
						transition-delay: 0.7s;
					}
				}

				.text {
					opacity: 1;
					transform: translate3d(0, 0, 0);
					-webkit-transform: translate3d(0, 0, 0);
					-moz-transform: translate3d(0, 0, 0);
					-ms-transform: translate3d(0, 0, 0);
					transition: transform .25s $transAboutTxtTiming, opacity 0.2s ease;
					transition-delay: 0.4s;
				}

				.comm-text {
					.text-inner {
						opacity: 1;
						transform: translate3d(0, 0, 0);
						-webkit-transform: translate3d(0, 0, 0);
						-moz-transform: translate3d(0, 0, 0);
						-ms-transform: translate3d(0, 0, 0);
						transition: transform .2s $transAboutTxtTiming, opacity 0.2s ease;
						transition-delay: 0.5s;
					}
				}

				&:after {
					transform: scale3d(1, 1, 1);
					-webkit-transform: scale3d(1, 1, 1);
					-moz-transform: scale3d(1, 1, 1);
					-ms-transform: scale3d(1, 1, 1);
					transition: transform 0.5s $transAboutTxtTiming;
					-webkit-transition: transform 0.5s $transAboutTxtTiming;
					transition-delay: 0.2s;
				}
			}
		}

	}
}

