// s: error
.error-container {
	@include justify_width;
	padding: {
		top: 15px;
		bottom: 15px;
	}
	height: 100vh;
	flex-direction: column;
	-webkit-flex-direction: column;
	-ms-flex-direction: column;

	.error-info {
		@include justify_width(center);
		flex-direction: column;
		-webkit-flex-direction: column;
		-ms-flex-direction: column;

		flex: 1;
		text-align: center;

		.error-type,
		.error-message {
			display: block;
			font-size: 2.8rem;
			line-height: 3.6rem;
			font-style: italic;
		}

		.error-message span,
		.error-type {
			text-decoration: underline;
		}

		.error-code {
			display: block;
			margin-top: 20px;
			font-size: $bodyText4;
			line-height: 1.6rem;
		}

		.error-desc {
			margin-top: 15px;
			font-size: $bodyText6;
			line-height: 1.8rem;
			color: $colorDarkGray;
			word-break: keep-all;
			word-wrap: break-word;
		}
	}

}

