@import 'scss_header';

.equal-wrap {
	margin-bottom: -30px;

	.equal-header {
		position: relative;
		padding: {
			top: 30px;
			bottom: 30px;
		}

		.util-wrap {
			position: absolute;
			top: 15px;
			left: 15px;
			right: 15px;
			@include justify_width(start);

			[class*=btn-link-] {
				position: relative;
				width: 30px;
				height: 30px;

				span {
					@include offscreen;
				}

				~ [class*=btn-link-] {
					margin-left: 5px;
				}
			}

			.btn-link-instagram {
				@include background_img("../images/equal/equal_btn_sns_instagram.png", 60px, 60px);
				display: inline-block;
			}

			.btn-link-youtube {
				@include background_img("../images/equal/equal_btn_sns_youtube.png", 60px, 60px);
				display: inline-block;
			}

			.btn-more-view {
				position: relative;
				display: inline-block;
				width: 28px;
				height: 28px;
				margin-left: auto;
				@include background_img("../images/equal/equal_btn_more_view.png", 56px, 56px);

				span {
					@include offscreen;
				}
			}
		}

		border-bottom: 1px solid $colorBlack;
		text-align: center;

		.btn-deit {
			display: inline-block;
			width: 44px;
			height: 44px;
			margin-left: auto;
			@include background_img("../images/equal/comm_lnb_profile_edit.png", 88px, 88px);

			span {
				@include offscreen;
			}
		}

		.info-profile-wrap {
			position: relative;
			width: 80px;
			height: 80px;
			margin: 0 auto;

			.info-profile {
				overflow: hidden;
				width: 80px;
				height: 80px;
				/*margin: -15px auto 0;*/
				margin: 0 auto;
				border: solid 1px $colorLightenGray;
				border-radius: 80px;
				@include justify_width;
				align-items: center;
				background-size: cover;
				background-position: center center;
				background-repeat: no-repeat;

				.text {
					margin: 0 auto;
					font-size: 5rem;
					font-weight: 300;
				}
			}

			.btn-deit {
				position: absolute;
				bottom: -12px;
				right: -9px;
			}
		}

		.info-name {
			margin-top: 15px;

			.text {
				font-size: 1.6rem;
				font-weight: 700;
				line-height: 2.1rem;
				vertical-align: top;
			}

			.label-insider {
				@include background_img("../images/equal/equal_bg_insider.png", 104px, 42px);
				overflow: hidden;
				display: inline-block;
				width: 52px;
				height: 21px;
				margin-left: 5px;
				text-align: center;
				font-size: 0.9rem;
				font-weight: 500;
				font-style: italic;
				line-height: 2.1rem;
				color: #fff;
				vertical-align: top;
			}
		}

		.more-text-wrap {
			margin-top: 15px;

			.text {
				font-size: $bodyText6;
				line-height: 1.8rem;
				letter-spacing: -0.06px;
			}

			.delete-text {
				vertical-align: 0;
				line-height: 0;
			}

			.tt-more {
				margin-left: 5px;
				font-size: 0;

				&:before {
					content: 'more';
					font-size: $bodyText5;
					font-weight: 500;
					line-height: 18px;
					color: $pointColor;
				}
			}

			&.is-more {
				.delete-text {
					display: inline-block;
					.tt-more:before {
						content: 'close';
					}
				}
			}
		}

		.equal-follower {
			margin-top: 5px;

			.count {
				display: inline-block;
				margin-left: 10px;
				padding: 3px 0;
				vertical-align: top;

				.count-number {
					display: inline-block;
					font-size: $bodyText5;
					font-weight: 300;
					line-height: 1.4rem;
					letter-spacing: normal;
					text-align: right;
					color: var(--black);
				}

				.count-title {
					display: inline-block;
					margin-left: 5px;
					font-size: $bodyText6;
					font-weight: 300;
					line-height: 1.4rem;
					color: $colorLightGray;
				}
			}


			.toggle-equal {
				display: inline-block;
				vertical-align: top;

				input {
					@include offscreen;

					~ label {
						display: inline-block;
						overflow: hidden;
						position: relative;
						width: 20px;
						height: 20px;

						&:after,
						&:before {
							@include background_img("../images/equal/equal_btn_follower.png", 80px, 40px);
							content: '';
							overflow: hidden;
							display: inline-block;
							width: 20px;
							height: 20px;
						}

						&:after {
							position: absolute;
							top: 0;
							left: 0;
							background-position: right 0;
							opacity: 0;
							-webkit-transition: opacity 0.5s $transDefaultEase;
							transition: opacity 0.5s $transDefaultEase;
						}
					}

					&:checked {
						~ label {
							&:after {
								opacity: 1;
							}
						}
					}
				}
			}
		}
	}

	.tab-content {
		.prod-grid-list {
			padding-bottom: 30px;
		}

		.box-list-wrap {
			&.brand-type {
				padding-bottom: 30px;
			}
		}

		.btn-more {
			margin-bottom: 0;
		}
	}
}

.equal-more-list {
	height: 180px;
	padding: 15px 0;

	.item {
		height: 51px;
		border-bottom: 1px solid #f3f3f3;

		.btn-sns,
		.toggle-equal label {
			display: block;
			width: 100%;
			padding: 15px;
			font-size: 1.6rem;
			line-height: 2.1rem;
			text-align: center;
			text-transform: uppercase;
		}

		.toggle-equal {
			input {
				@include offscreen;

				&:checked {
					~ label {
						&:after {
							content: ' 취소';
						}
					}
				}
			}
		}
	}
}

.flex-box-list-wrap {
	&.equal-type {
		overflow: hidden;
		margin-right: -1px;
		margin-bottom: -1px;
		.flex-box-item {
			position: relative;
			border-bottom: 1px solid $colorBlack;

			&:before {
				z-index: 2;
				content: '';
				display: block;
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				width: 1px;
				background: $colorBlack;
			}

			.detail-cont-box {
				height: 100%;

				.ratio-img-wrap {
					height: 100%;
					text-align: center;

					.img-name {
						overflow: hidden;
						position: absolute;
						top: 0;
						right: 0;
						left: 0;
						bottom: 0;
						display: block;
						@include justify_width;
						align-items: center;

						.text {
							flex: 1;
							display: block;
							font-size: $bodyText2;
							font-weight: 500;
							line-height: 2rem;
							word-break: break-all;
							text-align: center;
						}
					}

					.img-viewer {
						padding-top: 100%;
					}
				}
			}

			.btn-item-equal {
				z-index: 3;
				position: absolute;
				bottom: 0;
				right: 1px;
			}
		}

	}
}