@import 'scss_header';

// s: 주문 현황
.order-status-list {
	@include justify_width;
	align-items: center;
	height: 82px;
	border-bottom: 1px solid $colorLightestGray;

	.item {
		overflow: hidden;
		position: relative;
		flex: 1;
		width: 20%;
		padding: 0 5px;
		text-align: center;

		.value {
			@include ellipsis;
			display: block;
			font-size: 1.8rem;
			line-height: 2.3rem;

			&.disabled {
				color: $colorLightGray;
				
				pointer-events: none;
			}
		}

		.status {
			overflow: hidden;
			white-space: nowrap;
			display: block;
			margin-top: 4px;
			font-size: 1rem;
			font-weight: 300;
			line-height: 1.5rem;
		}

		~ .item {
			&:before {
				content: '';
				position: absolute;
				top: 50%;
				left: -5px;
				width: 10px;
				height: 10px;
				margin-top: -5px;
				@include background_img('../images/mypage/order_status_arr.png', 20px, 20px);
			}
		}
	}
}

// e: 주문 현황

// s: 주문 번호 목록
.order-number-list-wrap {
	padding-top: 5px;
}

.order-number-item {
	padding-top: 15px;

	.prod-item {
		.btn-even-wrap {
			[class*=btn] {
				flex: 1 1 calc((100% - 10px) / 3);

				&:nth-child(even) {
					flex: 0 1 calc((100% - 10px) / 3);
				}
			}
		}
	}

	.prod-action-info {
		margin-bottom: 10px;
	}

	~ .order-number-item {
		margin-top: 25px;
	}
}

// e: 주문 번호 목록

// s: 주문 단계 안내
.order-step-box-wrap {
	padding-top: 15px;
	border-top: 1px solid $colorBlack;

	.step-box-item {
		@include justify_width(start);
		border: solid 1px #ccc;
		padding: 15px 14px;

		.step-num {
			width: 56px;
			font-size: $bodyText5;
			font-weight: 500;
			line-height: 1.8rem;
			letter-spacing: -0.3px;
		}

		.step-info {
			flex: 1 0 calc(100% - 56px);

			.step-title {
				font-size: $bodyText5;
				line-height: 1.8rem;
				letter-spacing: -0.3px;
			}

			.step-desc {
				margin-top: 6px;
				font-size: $bodyText6;
				line-height: 1.7rem;
				color: $colorLightGray;
			}
		}

		~ .step-box-item {
			margin-top: -1px;
		}
	}
}

// e: 주문 단계 안내

.prod-action-info {
	@include justify_width;
	flex-wrap: wrap;
	margin-bottom: 11px;

	.order-num, .order-date, .claim-num {
		display: block;
		font-size: $bodyText4;
		color: $colorBlack;
		line-height: 1.6rem
	}

	.claim-num {
		flex: 0 1 100%;
		padding: 10px 15px 9px;
		margin-bottom: 10px;
		line-height: 1.6rem;
		background: $colorLightestGray;
	}

	.order-date {
		margin-left: auto;
	}

}

// s: 안내 박스
.line-guide-box {
	padding: 15px 15px 14px;
	margin: {
		top: 15px;
		bottom: 15px;
	}
	border: solid 1px $colorLightenGray;

	.guide-title {
		font-size: $bodyText5;
		line-height: 1.8rem;
	}

	.text-list {
		margin-top: 8px;

		li {
			color: $colorGray;

			~ li {
				margin-top: 6px;
			}
		}
	}
}

// e: 안내 박스

//s : 포인트/마일리지/회원등급 현황
.tab-wrap {
	.tab-content {
		.benefit-status-wrap {
			.benefit-status-list {
				margin-top: 20px;
			}

			~ .text-list {
				margin-top: 20px;
			}
		}
	}
}

.benefit-status-wrap {
	.benefit-status-list {
		margin-top: 30px;

		li {
			@include justify_width;
			align-items: center;

			.benefit-type {
				display: block;
				margin-right: 10px;
				font-size: $bodyText5;
				line-height: 1.9rem;
			}

			.benefit-value {
				flex: 1;
				display: block;
				font-size: 1.8rem;
				line-height: 2.3rem;
				word-break: break-all;
				text-align: right;

				&:after {
					content: 'P';
					display: inline-block;
					margin-left: 4px;
					vertical-align: top;
				}
			}

			.benefit-date {
				flex: 1;
				display: block;
				font-size: 1.8rem;
				line-height: 2.3rem;
				word-break: break-all;
				text-align: right;
			}

			.grade-value {
				font-size: 1.6rem;
				line-height: 2.3rem;

				.condition {
					font-size: $bodyText4;
					color: $colorGray;
				}
			}

			.grade-period {
				font-size: 1.4rem;
				line-height: 2.3rem;
			}

			~ li {
				margin-top: 10px;
			}
		}

		+ .text-list {
			margin-top: 30px;
			padding-top: 20px;
			border-top: 1px solid $colorLightenGray;
		}
	}

	&.mileage-type {
		.benefit-status-list li .benefit-value:after {
			content: 'M';
		}
	}
}


//e : 포인트/마일리지/회원등급 현황

//s : 포인트/마일리지 적립/사용내역
.benefit-action-wrap {
	margin-top: 20px;

	.benefit-action-list {
		border-top: 1px solid $colorBlack;

		li {
			@include justify_width;
			align-items: center;
			padding: 14px 0;
			min-height: 67px;
			border-bottom: 1px solid $colorLightestGray;

			.benefit-title {
				font-size: $bodyText5;
				letter-spacing: -0.03rem;
				@include multi_ellipsis(1.8rem);
				height: auto;
			}

			.benefit-info {
				flex-shrink: 0;
				min-width: 90px;
				padding-left: 10px;
				text-align: right;

				.value {
					display: block;
					font-size: $bodyText5;
					line-height: 1.8rem;

					&:after {
						content: 'P';
						display: inline-block;
						margin-left: 4px;
						vertical-align: top;
					}

					&.plus {
						&:before {
							content: '+';
						}
					}

					&.minus {
						&:before {
							content: '-';
						}
					}
				}

				.date {
					display: block;
					margin-top: 2px;
					font-size: $bodyText6;
					color: $colorLightGray;
					line-height: 1.4rem;
				}
			}
		}
	}

	&.mileage-type {
		.benefit-action-list li .benefit-info .value:after {
			content: 'M';
		}
	}
}

//e : 포인트 사용내역

//s : 쿠폰 등록
.coupon-register-wrap {
	padding-top: 15px;
	margin-bottom: 35px;

	.desc-black {
		margin-top: 10px;
		line-height: 1.8rem;
	}

	.text-list {
		margin-top: 30px;
	}
}

//e : 쿠폰 등록

//s : 쿠폰 리스트
.coupon-list-wrap {
	.coupon-list {
		margin-top: 20px;

		li {
			.item {
				position: relative;
				display: block;
				min-height: 133px;
				padding: 14px 15px;
				background: #fff;
				border: solid 1px #ddd;

				&:after,
				&:before {
					z-index: 3;
					content: '';
					position: absolute;
					top: 50%;
					bottom: 0;
					display: block;
					width: 4px;
					height: 109px;
					margin-top: -54px;
					@include background_img('../images/mypage/mypage_coupon.png', 8px, 218px);
				}

				&:after {
					right: -1px;
					transform: rotateY(180deg);
					-webkit-transform: rotateY(180deg);
					-moz-transform: rotateY(180deg);
				}

				&:before {
					left: -1px;
				}

				.benefit-info {
					padding-bottom: 15px;
					border-bottom: 1px solid $colorLightestGray;
					text-align: center;

					.coupon-value {
						display: block;
						font-size: 1.8rem;
						line-height: 2.5rem;

						.value {
							font-size: $bodyText1;
							font-weight: 500;
							vertical-align: -1px;
							font-style: italic;
						}
					}

					.coupon-condition {
						display: block;
						margin-top: 2px;
						font-size: $bodyText6;
						color: $colorLightGray;
						line-height: 1.8rem;
						letter-spacing: -0.028rem;
					}

					.coupon-period {
						display: block;
						margin-top: 2px;
						font-size: $bodyText6;
						line-height: 1.4rem;
						color: $colorLightGray;
						letter-spacing: -0.028rem;
					}
				}

				.coupon-info {
					padding: 10px 0 0;

					.coupon-name {
						font-size: $bodyText6;
						letter-spacing: -0.028rem;
						color: $colorDarkGray;
						@include multi_ellipsis(1.6rem);
						height: auto;
						word-break: keep-all;
						word-wrap: break-word;
					}

					.coupon-type {
						margin-top: 4px;

						.type, .num {
							display: inline-block;
							font-size: $bodyText6;
							line-height: 1.7rem;
							color: $colorGray;
							vertical-align: top;
							letter-spacing: -0.028rem;
						}

						.num {
							margin-left: 5px;
						}
					}
				}
			}

			~ li {
				margin-top: 15px;
			}
		}

		&.use-type {
			li {
				.item {
					background: #fafafa;
					&:after,
					&:before {
						background-image: url('../images/mypage/mypage_coupon_use.png');
					}
				}
			}
		}
	}

	.no-data {
		border-bottom: 1px solid $colorLightestGray;
	}
}

//e : 쿠폰 리스트

//s : 회원등급
.sctitle {
	+ .member-grade-wrap {
		margin-top: 0;
	}
}

.member-grade-wrap {
	margin-top: 30px;
	border-top: 1px solid $colorBlack;

	.grade-list {
		> li {
			@include justify_width;
			padding: 20px 0;
			border-bottom: 1px solid #f2f2f2;

			.grade-level {
				display: block;
				flex: 0 1 80px;
				margin: 4px 10px 0 0;
				font-size: 1.6rem;
				font-weight: 700;
				font-style: italic;
				text-transform: uppercase;
			}

			.grade-detail {
				flex: 1;

				.grade-condition {
					display: block;
					font-size: $bodyText4;
					line-height: 2.1rem;
					font-weight: 500;
				}

				.benefit-list {
					margin-top: 10px;

					li {
						font-size: $bodyText5;
						line-height: 1.8rem;

						.val {
							font-weight: 500;

							span {
								font-style: italic;
							}
						}
					}
				}

				.coupon-list {
					margin-top: 15px;
					font-size: 0;

					li {
						margin: 5px 0 0 0;
						vertical-align: top;

						.coupon-box {
							padding: 9px 3px 8px 15px;
							border: solid 1px $colorLightenGray;

							.text {
								display: inline-block;
								font-size: $bodyText5;
								line-height: 1.6rem;
								margin-right: 12px;
							}

							.text-desc {
								display: inline-block;
								margin-left: auto;
								font-size: $bodyText6;
								line-height: 1.6rem;
								color: $colorLightenGray;
								margin-right: 12px;
							}

							.spot {
								font-style: italic;
								font-weight: 400 !important;
							}
						}
					}

				}
			}

		}
	}

	.text-list {
		margin-top: 20px;
	}

}

//e : 회원등급

//s : 마이페이지 메인
.member-info-wrap {
	margin-top: 15px;
	border: 1px solid $colorBlack;
	box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);

	.profile-info {
		padding: 15px 15px 0;
		@include justify_width;

		.name {
			flex: 1;
			display: block;
			margin-right: 24px;
			font-size: 2.2rem;
			line-height: 2.8rem;
			word-break: break-word;
		}

		.my-profile-name {
			flex-shrink: 0;
			margin-left: auto;
		}
	}

	.grade-info {
		margin-top: 30px;
		padding: 0 15px;

		.grade-detail {
			// @include justify_width;
			// align-items: center;

			.grade-level {
				//display: block;
				font-size: 2.1rem;
				line-height: 2.6rem;
				vertical-align: middle;

				&.non-member {
					font-size: 1rem;
					padding-left: 10px;
				}
			}

			.grade-desc {
				display: block;
				padding-left: 15px;
				margin: {
					top: 3px;
					left: auto;
				}
				font-size: 1rem;
				font-weight: 300;
				line-height: 1.5rem;
				word-break: break-word;
			}
		}

		.progress-bar {
			margin-top: 9px;
		}

		.grade-guide {
			margin-top: 8px;
			@include justify_width;

			.desc {
				display: block;
				flex: 1;
				margin-right: 10px;
				font-size: 1rem;
				line-height: 1.8rem;
				font-weight: 300;
				word-break: keep-all;
				word-wrap: break-word;
			}

			.btn-link {
				align-self: flex-start;
				margin-top: 5px;
			}
		}

		&::after {
			content: '';
			display: block;	
			width: 100%;
			height: 3px;
			background: #f2f2f2;
			border-left: 1px solid #000;
			margin: {
				top: 9px;
				bottom: 8px;
			}
		}
	}

	.join-info {
		@include justify_width;
		padding: 0 15px;

		.join-text {
			font-size: 1rem;
			line-height: 1.5rem;
		}

		.join {
			font-size: 1.1rem;
			border-bottom: 1px solid #000;
		}

	}

	.benefit-info {
		margin-top: 30px;
		padding: 13px 15px 14px;
		border-top: 1px solid $colorLightenGray;

		.benefit-list {
			li {
				@include justify_width(start);
				align-items: center;

				.value {
					display: block;
					font-size: 2.1rem;
					line-height: 2.6rem;
					margin-right: 11px;
				}

				.type {
					display: block;
					font-size: 1rem;
					font-weight: 300;
					line-height: 1.5rem;
					letter-spacing: -0.08rem;
				}

				~ li {
					margin-top: 10px;
				}
			}
		}
	}
}

