.lnb-wrap {
	margin-bottom: 40px;

	.lnb {
		border-top: 1px solid $colorBlack;

		.item {
			border-bottom: 1px solid $colorLightestGray;

			.btn-lnb {
				display: block;
				font-size: $bodyText4;
				font-weight: 300;
				line-height: 39px;
			}
		}
	}
}

//e : 마이페이지 메인

.progress-bar {
	progress[value] {
		width: 100%;
		height: 3px;
		/* Reset the default appearance */
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;
		border: none;
		color: $colorBlack;

		&::-webkit-progress-bar { // chrome
			background-color: $colorLightestGray;
		}

		&::-moz-progress-bar { // firefox
			background-color: $colorLightestGray;
		}

		&::-webkit-progress-value {
			background-color: $colorBlack;
		}
	}
}


// s: 문의 내역 목록
.inquiry-history-list-wrap {
	.inquiry-history-list {
		margin-top: 40px;
		border-top: 1px solid $colorBlack;

		.list-item {
			position: relative;
			padding: 15px 0;
			border-bottom: solid 1px $colorLightestGray;

			&.is-answer {
				.btn-edit {
					display: none;
				}

				.inquiry-box {
					.inquiry-cont {
						.answer-state {
							color: $colorBlack;
						}
					}
				}
			}
		}

		.inquiry-box {
			min-height: 41px;
			@include justify_width;
			align-items: center;

			.inquiry-type {
				width: 55px;
				font-size: 1rem;
				line-height: 1.5rem;
				letter-spacing: -0.25px;
				color: $colorDarkGray;
			}

			.inquiry-cont {
				width: calc(100% - 55px);
				padding-left: 5px;
				align-self: flex-start;

				.answer-date {
					display: inline-block;
					font-size: $bodyText6;
					font-weight: 300;
					line-height: 1.7rem;
					vertical-align: top;
				}

				.answer-state {
					display: inline-block;
					margin-left: 5px;
					font-size: $bodyText6;
					line-height: 1.7rem;
					color: $colorLightGray;
					vertical-align: top;
				}

				.inquiry-desc {
					@include ellipsis;
					display: block;
					margin-top: 6px;
					font-size: $bodyText5;
					line-height: 1.8rem;
					color: $colorDarkGray;
				}
			}
		}

		.control-btn-wrap {
			position: absolute;
			top: 15px;
			right: 0;

			[class*=btn] {
				line-height: 1.4rem;

				~ [class*=btn] {
					margin-left: 8px;
				}
			}
		}
	}

	.no-data {
		margin-top: 20px;
	}
}

// e: 문의 내역 목록

// s: 이벤트 내역 목록
.event-history-list-wrap {
	.event-history-list {
		margin-top: 5px;

		.item {
			padding-top: 15px;

			.event-item-box {
				padding-bottom: 0;

				.detail-cont-box {
					@include justify_width;

					.ratio-img-wrap {
						width: 160px;
						@include mob_small {
							width: 145px;
						}
					}

					.event-cont {
						width: calc(100% - 160px);
						padding-left: 10px;
						@include mob_small {
							width: calc(100% - 145px);
						}

						.event-title {
							@include multi_ellipsis(2rem, 4);
							height: 85px;
							margin-top: 0;
							font-size: $bodyText4;
							line-height: 2rem;
							letter-spacing: -0.33px;
							color: $colorDarkGray;
							font-weight: 400;
						}

						.event-period {
							margin-top: 15px;
							@include mob_small {
								margin-top: 8px;
							}
						}
					}
				}
			}

			~ .item {
				margin-top: 15px;
				border-top: 1px solid $colorLightestGray;
			}
		}

	}

	.no-data {
		margin-top: 20px;
	}
}

// e: 이벤트 내역 목록

// s: 리뷰 내역 목록
.prod-history-list-wrap {
	.search-period-wrap {
		margin-bottom: 5px;
	}

	.fold-box-list {
		padding-top: 15px;
		border-bottom: 1px solid $colorLightestGray;

		.fold-box {
			border-top: 1px solid $colorBlack;

			.prod-item {
				padding: 20px 0;

				.info-wrap {
					.prod-brand-name {
						@include multi_ellipsis(1.7rem, 2);

						height: auto;
					}

					.prod-name {
						@include multi_ellipsis(1.6rem, 4);
					}
				}
			}

			.fold-box-header {
				border-top: 1px solid $colorLightestGray;

				.toggle-title-box {
					border-bottom: 0;
					padding: 15px 0;

					.toggle-title {
						@include justify_width;
						justify-items: flex-start;
						flex-wrap: wrap;
						width: calc(100% - 38px);
						@include mob_small {
							width: calc(100% - 30px);
						}

						.rating-list {
							width: calc(100% - 62px);
							font-size: 0;

							li {
								display: inline-block;
								font-size: 0;
								vertical-align: top;

								.rating-container {
									padding: 3px 0 2px;
									margin-right: 2px;
									vertical-align: top;
								}

								.type {
									display: inline-block;
									font-size: $bodyText6;
									line-height: 1.7rem;
									color: $colorLightGray;
								}

								.val {
									display: inline-block;
									margin-left: 4px;
									font-size: $bodyText6;
									line-height: 1.7rem;
									color: $colorBlack;
								}

								~ li {
									margin-left: 8px;
									@include mob_small {
										margin-left: 5px;
									}
								}
							}
						}

						.info-date {
							width: 62px;
							font-size: $bodyText6;
							line-height: 1.7rem;
						}

						.info-title {
							@include ellipsis;
							display: block;
							width: 100%;
							margin-top: 10px;
							font-size: $bodyText5;
							line-height: 1.8rem;
							color: $colorDarkGray;
							word-break: keep-all;
							word-wrap: break-word;
						}
					}

					.right-box {
						width: 38px;
						padding-right: 15px;
						@include mob_small {
							width: 30px;
							padding-right: 7px;
						}

						.ico-toggle {
							@include background_img('../images/ui/button/ui_btn_prod_control.png', 36px, 18px);
							width: 9px;
							height: 9px;
							background-position: -9px 0;

							&.is-reverse {
								background-position: 0 0;
							}
						}
					}
				}

				.review-img-thumb-wrap {
					@include justify_width;
					position: relative;
					margin-bottom: 15px;
					.review-img-thumb {
						width: calc( 50% - 5px );
						height: auto;
						padding-top: 32.1212%;
						~ .review-img-thumb {
							margin-left: 10px;
						}
					}
					.review-img-count {
						position: absolute;
						bottom: 0;
						right: 0;
						width: 40px;
						height: 30px;
						background-color: rgba(255,255,255,0.7);
						color: $colorBlack;
						font-size: $bodyText4;
						line-height: 3rem;
						text-align: center;
					}
				}
			}

			.fold-box-contents {
				padding: {
					left: 0;
					right: 0;
				}

				.editor-wrap {
					font-size: $bodyText5;
					line-height: 1.8rem;
					letter-spacing: -0.3px;
					color: $colorDarkGray;
				}

				.control-btn-wrap {
					margin-top: 15px;
					text-align: right;

					[class*=btn] {
						~ [class*=btn] {
							margin-left: 8px;
						}
					}
				}
				.review-desc-img {
					margin-top: 5px;
					.item-img {
						margin-top: 5px;
						img {
							max-width: 100%;
						}
					}
				}
			}

			&.expanded {
				.fold-box-header {
					.toggle-title-box {
						border-bottom: 1px solid $colorLightestGray !important;
					}
					.review-img-thumb-wrap {
						display: none;
					}
				}

				.fold-box-contents {
					border-bottom: 0;
					padding-bottom: 15px;
				}
			}
		}
	}

	.no-data {
		margin-top: 20px;
	}

	&.qna-type {
		.fold-box {
			.fold-box-header {
				.toggle-title-box {
					.toggle-title {
						.answer-state {
							width: calc(100% - 62px);
							flex: 1;
							margin-left: 10px;
							font-size: $bodyText6;
							line-height: 1.7rem;
							color: $colorLightGray;
						}
					}
				}
			}

			.answer-text,
			.question-text {
				position: relative;
				padding-left: 15px;
				font-size: $bodyText5;
				line-height: 1.8rem;
				letter-spacing: -0.3px;
				color: $colorDarkGray;
				word-break: keep-all;
				word-wrap: break-word;

				&:before {
					overflow: hidden;
					position: absolute;
					top: 0;
					left: 0;
					display: block;
					width: 15px;
					font-size: $bodyText5;
					font-weight: 500;
					line-height: 1.8rem;
					letter-spacing: -0.6px;
					color: $colorDarkGray;
				}
			}

			.question-text {
				&:before {
					content: 'Q';
				}
			}

			.answer-text {
				display: none;
				margin-top: 15px;

				&:before {
					content: 'A';
				}
			}

			&.is-answer {
				.fold-box-header {
					.toggle-title-box {
						.toggle-title {
							.answer-state {
								color: $colorBlack;
							}
						}
					}
				}

				.answer-text {
					display: block;
				}

				.btn-edit {
					display: none;
				}
			}
		}
	}
}

// e: 리뷰 내역 목록

// s: 문의폼
.tbl-form {
	&.inquiry-form {
		.form-box {
			.prod-item {
				.prod-desc {
					display: none;
				}

				.info-wrap {
					@include justify_width(start);
					flex-direction: column;
					justify-items: flex-start;

					.prod-name {
						@include multi_ellipsis(1.8rem, 2);
					}

					.btn-control-wrap {
						margin-top: auto;
					}
				}
			}

			&.attach-file {
				margin-bottom: 15px;
			}

			&.inquiry-prod {
				display: none;

				&.is-active {
					display: block;
				}
			}
		}
	}

	.prod-item {
		&.inquiry-prod-item {
			.prod-action-info {
				text-align: left;
				margin-bottom: 6px;
				display: block;

				.order-num {
					font-size: $bodyText5;
					line-height: 1.9rem;
				}

				.order-date {
					font-size: $bodyText5;
					line-height: 1.9rem;
				}
			}

			.info-wrap {
				.prod-brand-name {
					margin-top: 0;
					font-size: $bodyText5;
				}

				.prod-name {
					@include multi_ellipsis(2rem, 2);
					margin-top: 4px;
					font-size: $bodyText5;
				}
			}

			.ratio-img-wrap {
				margin-top: 6px;
				width: 75px;
			}
		}
	}
}

// e: 문의폼

// s: 주문상품 선택 팝업
.ui-dialog-container {
	&.full {
		.ui-dialog-contents {
			.dialog-contents-flex {
				.search-period-wrap {
					.btn-even-wrap {
						margin-top: 10px;
						padding-bottom: 0;
					}
				}
			}

			.prod-sel-list-wrap {
				margin-top: 10px;

				.no-select {
					margin-top: 20px;
					padding: 15px 0;
					border: {
						top: 1px solid $colorLightestGray;
						bottom: 1px solid $colorLightestGray;
					}
				}

				.ui-rdo {
					input {
						+ label {
							padding-left: 24px;
						}
					}
				}

				.prod-sel-list {
					.item {
						padding: 15px 0;
						border-bottom: 1px solid $colorLightestGray;

						.ui-rdo {
							width: 100%;

							input+label {
								width: 100%;
							}
						}

						.prod-action-info {
							display: block;
							margin-bottom: 15px;

							.order-num,
							.order-date {
								display: inline-block;
								font-size: $bodyText4;
								line-height: 1.9rem;
							}
						}

						.prod-item {
							display: block;

							.prod-link {
								color: $colorBlack;
								align-items: flex-start;
								-webkit-align-items: flex-start;

								.prod-desc {
									display: none;
								}

								.info-wrap {
									.prod-name {
										@include multi_ellipsis(1.8rem, 2);
										margin-top: 4px;
									}
								}
							}
						}
					}
				}

			}
		}
	}
}

// s: 1:1 문의 상세 > 답변완료
.answer-complete-wrap {
	border-top: 1px solid $colorBlack;

	.answer-item-box {
		padding: 15px 0 20px;
		border-bottom: 1px solid $colorLightestGray;

		.answer-header {
			@include justify_width;
			align-items: center;
			-webkit-align-items: center;

			.answer-tit {
				flex: 1;
				font-size: $bodyText5;
				line-height: 1.8rem;
			}

			.answer-date {
				font-size: $bodyText6;
				font-weight: 300;
				line-height: 1.8rem;
			}
		}

		.answer-content {
			margin-top: 8px;

			.editor-wrap {
				font-size: $bodyText5;
				line-height: 1.8rem;
			}
		}
	}

	.satisfaction-box {
		margin-top: 40px;
		padding: 20px 0 0;
		border: 1px solid $colorLightenGray;

		.satisfaction-title {
			font-size: $bodyText3;
			line-height: 2rem;
			text-align: center;
		}

		.satisfaction-quest {
			margin-top: 13px;
			padding: 0 0 0 10px;
		}

		.btn-even-wrap {
			border-top: 1px solid $colorLightestGray;
		}
	}
}

// e: 1:1 문의 상세 > 답변완료

// s: 회원정보
.tbl-form {
	&.member-info-type {
		padding-top: 10px;

		.form-box {
			align-items: center;

			&.horizontal {
				~ .form-box {
					&.horizontal {
						margin-top: 14px;

						&.has-btn {
							margin-top: 5px;
							margin-bottom: -8px;
						}
					}

					&:not(.horizontal) {
						.form-title {
							margin-top: 14px;
						}
					}
				}
			}

			.form-title, .form-cont {
				line-height: 1.8rem;
			}
		}
	}
}

// e: 회원정보
